<template>
<md-dialog :md-active.sync="showDialog">
  <md-dialog-title></md-dialog-title>
  <md-dialog-content class="viewer-contaner">
    <md-chip :class="{'md-primary': isActive(tag)}" md-clickable v-for="(tag, index) in selections" :key="index" @click="taging(tag)">{{tag}}</md-chip>
  </md-dialog-content>
  <md-dialog-actions>
    <md-button class="md-primary" @click="showDialog = false"></md-button>
    <md-button class="md-primary" @click="excute"></md-button>
  </md-dialog-actions>
</md-dialog>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  data: () => ({
    showDialog: false,
    tags: [],
    selections: [],
    next: null
  }),
  mounted () {
    const that = this
    utils.event.$on('EDIT_TAG', (tags, selections, next) => {
      that.tags.splice(0, that.tags.length)
      that.selections.splice(0, that.selections.length)
      if (tags) {
        for (let i = 0; i < tags.length; i++) {
          that.tags.push(tags[i])
        }
      }
      if (selections) {
        for (let i = 0; i < selections.length; i++) {
          that.selections.push(selections[i])
        }
      }
      that.next = next || null
      that.$nextTick(() => {
        that.showDialog = true
      })
    })
  },
  beforeDestroy () {
    utils.event.$off('EDIT_TAG')
    this.tags.splice(0, this.tags.length)
    this.next = null
  },
  methods: {
    isActive (tag) {
      return this.tags.some(one => {
        return one === tag
      })
    },
    taging (tag) {
      if (!this.isActive(tag)) {
        this.tags.push(tag)
      } else {
        for (let i = 0; i < this.tags.length; i++) {
          if (this.tags[i] === tag) {
            this.tags.splice(i, 1)
            return
          }
        }
      }
    },
    excute () {
      if (this.next) this.next(this.tags)
      this.showDialog = false
    }
  }
}
</script>

<style scoped lang="scss">

</style>



