<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary switch-config" @click="config.hide = !config.hide">
      <md-icon v-if="!config.hide">visibility_off</md-icon>
      <md-icon v-else>visibility</md-icon>
    </md-button>
    <md-button class="md-accent hp-more" @click="manager.homepageService.switchPage('rentPieces')">
      more
    </md-button>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="md-layout md-gutter" v-if="!config.hide">
    <div :class="getPieceClass(index)" v-for="(piece, index) in manager.homepageService.dummyRentPieces" :key="piece._id" v-if="index < parseInt(config.count)">
      <rentPieceCard2 :manager="manager" :object="piece" :design="config.design" :tags="['']"/>
    </div>
  </div>
  <div class="visibility-off" v-else>
    
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import rentPieceCard2 from '../shared/rentPieceCard2'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    rentPieceCard2
  },
  methods: {
    getPieceClass (index) {
      let classes = ''
      switch (parseInt(this.config.count)) {
        case 2:
          classes = 'md-layout-item md-size-50 md-small-size-100'
          break
        case 3:
          classes = 'md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100'
          break
        case 4:
          classes = 'md-layout-item md-size-25 md-medium-size-50 md-xsmall-size-100'
          break
        default:
          classes = 'md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100'
          break
      }
      if (parseInt(this.config.count) !== 2 && parseInt(this.config.count) !== 4) {
        if (index === 2) classes += ' bd-medium-hide'
      }

      return classes
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout {
  min-height: 348px;
}
.switch-config {
  position: absolute;
  top: 6px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.visibility-off {
  width: 100%;
  height: 348px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e8e8e8;
}
</style>



