<template>
<div>
  <vue-headful title=" - "/>

  <div class="info-title">
    <md-content class="title-bar md-primary"></md-content><p class="md-title"> ({{manager.topicService.topics.length}})</p>
  </div>
  <div class="actions">
    <md-button class="md-icon-button md-raised md-accent" @click="add"><md-icon>add</md-icon></md-button>
  </div>
  <topicLable :manager="manager" :topic="topic" :index="index" v-for="(topic, index) in manager.topicService.topics" :key="topic._id"/>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import Topic from '@/store/models/topic.js'
import topicLable from '@/components/shared/topicLable'
export default {
  props: ['manager'],
  components: {
    topicLable
  },
  methods: {
    add () {
      manager.topicService.topics.unshift(new Topic({user: manager.user._id}))
    }
  }
}
</script>

<style scoped lang="scss">
.info-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
  margin-right: 10px;
}
.actions {
  position: absolute;
  top: 100px;
  right: 10px;
}
.topic {
  position: relative;
}
</style>



