<template>
<md-card ref="animate" md-with-hover>
  <md-card-media class="ribbon-box">
    <div class="ribbon-area" v-if="design === '1' && tag">
      <span :class="ribbonClass"><span class="animate">{{tag}}</span></span>
    </div>
    <md-content :class="ribbon2Class" v-else-if="design === '2' && tag">
      <span><span class="animate">{{tag}}</span></span>
    </md-content>
    <img class="piece-thumbnail" :src="object.photoThumbnails[0]" v-if="object.photoThumbnails && object.photoThumbnails.length > 0">
  </md-card-media>

  <md-card-header>
    <div class="md-title">{{object.name}}</div>
    <div class="md-subhead" v-if="object.stations && object.stations.length > 0">
      {{object.stations[0].station}} {{object.stations[0].walking}}
    </div>
    <div class="md-subhead" v-else></div>
  </md-card-header>

  <md-card-content>
    <md-content class="badge" @click="tooltipActive1 = !tooltipActive1">
      {{object.rentText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive1">{{object.rentText}}</md-tooltip>
    </md-content>
    <md-content :class="{'badge': true, 'muted': !object.deposit}" @click="tooltipActive2 = !tooltipActive2">
      
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive2">{{object.depositText}}</md-tooltip>
    </md-content>
    <md-content :class="{'badge': true, 'muted': !object.keyMoney}" @click="tooltipActive3 = !tooltipActive3">
      
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive3">{{object.keyMoneyText}}</md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.layout" @click="tooltipActive4 = !tooltipActive4">
      {{object.layoutText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive4">{{object.layoutText}}</md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive4 = !tooltipActive4">
      
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive4"></md-tooltip>
    </md-content>
    <md-content class="badge" v-if="object.exclusiveArea" @click="tooltipActive5 = !tooltipActive5">
      {{object.exclusiveAreaText}}
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive5">{{object.exclusiveAreaText}}</md-tooltip>
    </md-content>
    <md-content class="badge muted" v-else @click="tooltipActive5 = !tooltipActive5">
      
      <md-tooltip md-direction="top" :md-active.sync="tooltipActive5"></md-tooltip>
    </md-content>
  </md-card-content>
</md-card>
</template>

<script>
import anime from 'animejs'
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'object', 'design', 'tags'],
  data: () => ({
    tooltipActive1: false,
    tooltipActive2: false,
    tooltipActive3: false,
    tooltipActive4: false,
    tooltipActive5: false,
    tag: null,
    index: null
  }),
  mounted () {
    this.switchTag()
    this.animate()
  },
  computed: {
    ribbonClass () {
      return 'ribbon ' + manager.homepageService.homepage.theme + '-accent-gradient ' + manager.homepageService.homepage.theme + '-accent-ribbon'
    },
    ribbon2Class () {
      return 'md-accent ribbon2 ' + manager.homepageService.homepage.theme + '-accent-ribbon2'
    }
  },
  methods: {
    switchTag () {
      if (!this.tags || this.tags.length <= 0) return
      if (this.index === null) {
        this.index = 0
      } else if (this.index >= this.tags.length - 1) {
        this.index = 0
      } else {
        this.index++
      }
      this.tag = this.tags[this.index]
    },
    animate () {
      if (this.tags && this.tags.length > 1) {
        this.$nextTick(() => {
          if (this.$refs.animate) {
            let timeline = anime.timeline()
            timeline.add({
              targets: this.$refs.animate.$el.getElementsByClassName('animate'),
              opacity: [0, 1],
              easing: 'easeInCubic',
              duration: 200
            }).add({
              targets: this.$refs.animate.$el.getElementsByClassName('animate'),
              opacity: [1, 1],
              easing: 'easeInCubic',
              duration: 1000
            }).add({
              targets: this.$refs.animate.$el.getElementsByClassName('animate'),
              opacity: [1, 0],
              easing: 'easeInCubic',
              duration: 200,
              complete: () => {
                this.switchTag()
                this.animate()
              }
            })
          }
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}

.ribbon-box {
  position: relative;
  box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
  pointer-events: none;
}
.ribbon {
  display: inline-block;
  position: absolute;
  padding: 7px 0;
  left: -23px;
  top: 22px;
  width: 160px;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: "";
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before {
  left: 14px;
}
.ribbon:after {
  right: 18px;
}

.ribbon2 {
  display: inline-block;
  position: absolute;
  top: -6px;
  right: 10px;
  margin: 0;
  padding: 10px 0 7px;
  z-index: 2;
  width: 60px;
  text-align: center;
  color: #fff;
  font-size: 13px;
  border-radius: 2px 0 0 0;
}
.ribbon2:before {
  position: absolute;
  content: '';
  top: 0;
  right: -5px;
  border-right: solid 5px transparent;
}
.ribbon2:after {
  content: '';
  position: absolute;
  left: -2px;
  top: 98%;
  height: 0;
  width: 0;
  border-left: 32px solid transparent;
  border-right: 32px solid transparent;
}

.piece-thumbnail {
  cursor: pointer;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.md-title {
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-subhead {
  height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-card-content {
  display: flex;
  justify-content: flex-start;
}
.badge {
  background: #e8e8e8;
  padding: 8px;
  height: 32px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 3px;
  white-space: nowrap;
  overflow: hidden;
}
.muted {
  color: #aaa !important;
}
.piece-actions {
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 5px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



