import utils from '@/tool/utils.js'

class PurchaseService {
  purchase (message, next) {
    utils.restPut('/private/insertPurchase', message).then(
      response => {
        if (response) {
          if (next) next(response)
        }
      }
    )
  }
}

export default new PurchaseService()



