<template>
<div class="md-layout service-card">
  <div class="md-layout-item md-size-10 bd-xsmall-hide"></div>
  <div class="md-layout-item md-size-30 md-xsmall-size-100 photo-frame">
    <img class="service-photo" :src="object.photo">
    <verboseUploadFile class="photo-uploader" ref="photoUploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/resources/service'" :next="uploaded" v-if="edit"/>
  </div>
  <div class="md-layout-item md-size-10 bd-xsmall-hide"></div>
  <div class="md-layout-item md-size-40 md-xsmall-size-100 service-detail">
    <p class="md-display-1">{{object.title}}</p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-title" @click="editTitle = true" v-if="edit">
      <md-icon>edit</md-icon>
    </md-button>
    <p class="md-body-1">{{object.description}}</p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-service" @click="editService = true" v-if="edit">
      <md-icon>edit</md-icon>
    </md-button>
  </div>

  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary add-service" @click="addService()" v-if="!edit">
    <md-icon>add</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-service" @click="deleteService(index)" v-if="edit">
    <md-icon>close</md-icon>
  </md-button>
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editTitle"
    v-model="object.title"
    md-title=""
    md-input-maxlength="15"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editService"
    v-model="object.description"
    md-title=""
    md-input-maxlength="200"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import verboseUploadFile from '@/components/common/verboseUploadFile'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    user: {
      default: 'user'
    },
    object: {
      default: 'object'
    },
    edit: {
      type: Boolean,
      default: true
    },
    index: {
      default: null
    }
  },
  components: {
    verboseUploadFile
  },
  data: () => ({
    editTitle: false,
    editService: false
  }),
  methods: {
    addService () {
      if (!this.user.services) this.user.services = []
      this.user.services.push(this.object)
    },
    deleteService (index) {
      this.user.services.splice(index, 1)
    },
    uploaded (photo) {
      this.object.photo = photo.file
    }
  }
}
</script>

<style scoped lang="scss">
.service-card {
  margin: 80px 0;
  position: relative;
}
.photo-frame {
  position: relative;
}
.service-photo {
  width: 100%;
  height: 240px;
  object-fit: cover;
}
.photo-uploader {
  position: absolute;
  bottom: 6px;
  right: 0px;
}
.service-detail {
  position: relative;
}
.delete-service {
  position: absolute;
  top: 0;
  right: 0;
}
.add-service {
  position: absolute;
  bottom: 0;
  right: 0;
}
.edit-title {
  position: absolute;
  top: 28px;
  right: 0;
}
.edit-service {
  position: absolute;
  bottom: 8px;
  right: 0;
}
</style>



