<template>
<div>
  <p class="md-display-1"></p>
  <div class="topics">
    <infoLable :manager="manager" :object="info" v-for="info in manager.homepageService.dummyInfos" :key="info._id"/>
  </div>
</div>
</template>

<script>
// import manager from '@/store/manager.js'
// import utils from '@/tool/utils.js'
import topicLable from '../shared/topicLable'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    topicLable
  }
}
</script>

<style scoped lang="scss">
.md-display-1 {
  text-align: center;
}
.topics {
  padding-left: 40px;
  padding-right: 40px;
}
</style>



