import moment from 'moment'

class Inquiry {
  constructor (data) {
    this._id = data._id
    this.name = data.name
    this.contact = data.contact
    this.content = data.content
    this.mail = data.mail
    this.finished = data.finished || false
    this._cdate = null
    if (data.cdate) this.cdate = data.cdate
  }
  get cdate () {
    return this._cdate ? this._cdate.valueOf() : null
  }
  get cdateText () {
    return this._cdate != null ? this._cdate.format('YYYYMD') : '-'
  }
  set cdate (data) {
    if (data) this._cdate = moment(data)
    else this._cdate = null
  }

  toJSON () {
    return {
      _id: this._id,
      name: this.name,
      contact: this.contact,
      content: this.content,
      mail: this.mail,
      finished: this.finished
    }
  }
}

export default Inquiry



