<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"> ({{manager.homepageService.dummyRentPieces.length}} / {{manager.homepageService.dummyRentPieces.length}})</p>
    <md-radio class="bd-small-hide option" v-model="type" value="1"></md-radio>
    <md-radio class="bd-small-hide option" v-model="type" value="3"></md-radio>
    <md-radio class="bd-small-hide option" v-model="type" value="2"></md-radio>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="bd-small-show">
    <md-radio class="option" v-model="type" value="1"></md-radio>
    <md-radio class="option" v-model="type" value="3"></md-radio>
    <md-radio class="option" v-model="type" value="2"></md-radio>
  </div>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="piece in manager.homepageService.dummyRentPieces" :key="piece._id">
      <rentPieceCard2 :manager="manager" :object="piece" :design="config.design" :tags="piece.tags"/>
    </div>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import rentPieceCard2 from '../shared/rentPieceCard2'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    rentPieceCard2
  },
  data: () => ({
    type: '1'
  })
}
</script>

<style scoped lang="scss">
.component-wrapper {
  min-height: 300px;
}
.option {
  margin-left: 20px;
}
.md-title {
  width: 240px;
}
.md-layout {
  margin-top: 20px;
  min-height: 368px;
}
.md-layout-item {
  margin-bottom: 20px;
}
</style>



