import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import uuid from 'uuid'
import Homepage from '@/store/models/homepage.js'
import SellPiece from '@/store/models/sellPiece.js'
import RentPiece from '@/store/models/rentPiece.js'
import Topic from '@/store/models/topic.js'

class HomepageService {
  constructor () {
    this.homepages = []
    this.homepage = null
    this.showTemplate = false
    this.page = null
    this.dummySellPieces = []
    this.dummySellPieceData = {type: '1', name: '', price: 30000000, layout: '2DK', exclusiveArea: 58, stations: [{station: '-', walking: 15}], photos: [], photoThumbnails: []}
    this.dummyRentPieces = []
    this.dummyRentPieceData = {type: '2', name: '', rent: 120000, deposit: 1, keyMoney: 2, layout: '4LDK', exclusiveArea: 58, stations: [{station: '-', walking: 15}], photos: [], photoThumbnails: []}
    this.dummyTopics = []
    this.dummyTopicData = {type: 'News', idate: 1524139347451, text: ''}
  }

  init () {
    this.homepages.splice(0, this.homepages.length)
    this.homepage = null
    this.showTemplate = false
    this.page = null
    for (let i = 0; i < 12; i++) {
      this.dummySellPieceData._id = uuid.v4()
      this.dummySellPieceData.photos = ['/static/resources/piece/' + (i + 1) + '.jpg']
      this.dummySellPieceData.photoThumbnails = ['/static/resources/piece/' + (i + 1) + '.jpg']
      this.dummySellPieces.push(new SellPiece(this.dummySellPieceData))
      this.dummyRentPieceData._id = uuid.v4()
      this.dummyRentPieceData.photos = ['/static/resources/piece/' + (i + 13) + '.jpg']
      this.dummyRentPieceData.photoThumbnails = ['/static/resources/piece/' + (i + 13) + '.jpg']
      this.dummyRentPieces.push(new RentPiece(this.dummyRentPieceData))
    }
    for (let i = 0; i < 5; i++) {
      this.dummyTopicData._id = uuid.v4()
      this.dummyTopics.push(new Topic(this.dummyTopicData))
    }
  }

  deleteConfig (config) {
    if (!this.page) return
    for (let i = 0; i < this.page.topComponents.length - 1; i++) {
      let component = this.page.topComponents[i]
      if (component.id === config) {
        this.page.topComponents.splice(i, 1)
        return
      }
    }
    for (let i = 0; i < this.page.leftComponents.length - 1; i++) {
      let component = this.page.leftComponents[i]
      if (component.id === config) {
        this.page.leftComponents.splice(i, 1)
        return
      }
    }
    for (let i = 0; i < this.page.rightComponents.length - 1; i++) {
      let component = this.page.rightComponents[i]
      if (component.id === config) {
        this.page.rightComponents.splice(i, 1)
        return
      }
    }
    for (let i = 0; i < this.page.bottomComponents.length - 1; i++) {
      let component = this.page.bottomComponents[i]
      if (component.id === config) {
        this.page.bottomComponents.splice(i, 1)
        return
      }
    }
  }

  findHomepages (next) {
    let that = this
    utils.restGet('/private/findHomepages', {}).then(
      response => {
        if (response) {
          response.forEach(one => {
            that.homepages.push(new Homepage(one))
          })
          if (that.homepages.length <= 0) {
            that.homepages.push(new Homepage({domain: manager.user.homepages[0]}))
          }
          if (next) next()
        }
      }
    )
  }

  loadHomepages = (to, from, next) => {
    let that = this
    that.init()
    that.findHomepages(() => {
      that.homepage = that.homepages[0]
      if (!that.homepage.pages || that.homepage.pages.length <= 0) that.showTemplate = true
      else that.page = that.homepage.pages[0]
      if (next) next()
    })
  }

  insertHomepage (next) {
    let that = this
    utils.restPut('/private/insertHomepage', that.homepage).then(saved => {
      if (saved) {
        that.homepage._id = saved._id
        that.page = that.homepage.pages[0]
        if (next) next()
      }
    })
  }

  updateHomepage (next) {
    utils.restPost('/private/updateHomepage', this.homepage).then(saved => {
      if (saved) {
        if (next) next()
      }
    })
  }

  deleteHomepage (homepage, next) {
    utils.restPost('/private/deleteHomepage', {_id: homepage._id}).then(saved => {
      if (saved) {
        if (next) next()
      }
    })
  }

  switchPage (id) {
    this.page = null
    this.homepage.pages.forEach(page => {
      if (page.id === id) {
        this.page = page
      }
    })
    utils.event.$emit('SCROLL_TO_TOP')
  }
}

export default new HomepageService()



