<template>
<md-app :md-theme="manager.homepageService.homepage.theme" md-waterfall md-mode="fixed">

  <md-app-toolbar :class="barClass">
    <div class="md-toolbar-row">
      <div class="md-toolbar-section-start">
        <img class="resize-picture" :src="user.logo" v-if="user.logo">
        <verboseUploadFile class="logo-uploader" ref="logoUploader" :manager="manager" acceptType="image" type="icon" :subFolder="manager.user._id + '/resources/logo'" :next="uploaded"/>
        <p class="md-title" v-if="user.company">{{user.company}}</p>
        <md-button class="md-icon-button md-primary bd-small-show" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
      </div>
      <div class="md-toolbar-section-end">
        <md-tabs class="md-accent bd-small-hide" :md-active-tab="manager.homepageService.page.id">
          <md-tab
            v-for="page in manager.homepageService.homepage.pages"
            v-if="!page.hide"
            :key="page.id"
            :id="page.id"
            :md-icon="page.icon"
            :md-label="page.name"
            @click="manager.homepageService.switchPage(page.id)"
          ></md-tab>
        </md-tabs>
      </div>
    </div>
  </md-app-toolbar>

  <md-app-drawer :md-active.sync="menuVisible">
    <md-toolbar class="md-transparent" md-elevation="0">{{user.company}}</md-toolbar>
    <md-list>
      <md-list-item v-for="page in manager.homepageService.homepage.pages" :key="page.id" v-if="!page.hide" @click="manager.homepageService.switchPage(page.id)">
        <md-icon>{{page.icon}}</md-icon>
        <span class="md-list-item-text">{{page.name}}</span>
      </md-list-item>
    </md-list>
  </md-app-drawer>

  <md-app-content>
    <div class="app-content-auto-height" v-if="manager.homepageService.page">
      <component
        v-for="(config, index) in manager.homepageService.page.topComponents"
        :key="'top-' + index"
        :is="config.id"
        :manager="manager"
        :user="user"
        :config="config"
      ></component>
      <template v-if="manager.homepageService.page.leftComponents.length > 0 || manager.homepageService.page.rightComponents.length > 0">
        <div class="md-layout md-gutter">
          <div :class="leftClass">
            <component
              v-for="(config, index) in manager.homepageService.page.leftComponents"
              :key="'left-' + index"
              :is="config.id"
              :manager="manager"
              :user="user"
              :config="config"
            ></component>
          </div>
          <div :class="rightClass">
            <component
              v-for="(config, index) in manager.homepageService.page.rightComponents"
              :key="'right-' + index"
              :is="config.id"
              :manager="manager"
              :user="user"
              :config="config"
            ></component>
          </div>
        </div>
      </template>
      <component
        v-for="(config, index) in manager.homepageService.page.bottomComponents"
        :key="'bottom-' + index"
        :is="config.id"
        :manager="manager"
        :user="user"
        :config="config"
      ></component>
    </div>
  </md-app-content>

</md-app>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import cover from './component/cover'
import carousel from './component/carousel'
import agreedRentPieces from './component/agreedRentPieces'
import agreedRentPieces2 from './component/agreedRentPieces2'
import agreedSellPieces from './component/agreedSellPieces'
import agreedSellPieces2 from './component/agreedSellPieces2'
import investSellPieces from './component/investSellPieces'
import investSellPieces2 from './component/investSellPieces2'
import popularRentPieces from './component/popularRentPieces'
import popularRentPieces2 from './component/popularRentPieces2'
import popularSellPieces from './component/popularSellPieces'
import popularSellPieces2 from './component/popularSellPieces2'
import recommendRentPieces from './component/recommendRentPieces'
import recommendRentPieces2 from './component/recommendRentPieces2'
import recommendSellPieces from './component/recommendSellPieces'
import recommendSellPieces2 from './component/recommendSellPieces2'
import studentRentPieces from './component/studentRentPieces'
import studentRentPieces2 from './component/studentRentPieces2'
import newSellPieces from './component/newSellPieces'
import newSellPieces2 from './component/newSellPieces2'
import newRentPieces from './component/newRentPieces'
import newRentPieces2 from './component/newRentPieces2'
import services from './component/services'
import services2 from './component/services2'
import topics from './component/topics'
import topicCenter from './component/topicCenter'
import topicCenter2 from './component/topicCenter2'
import shops from './component/shops'
import shopInfo from './component/shopInfo'
import downloads from './component/downloads'
import links from './component/links'
import navigation from './component/navigation'
import sellPieces from './component/sellPieces'
import sellPieces2 from './component/sellPieces2'
import rentPieces from './component/rentPieces'
import rentPieces2 from './component/rentPieces2'
import companyInfo from './component/companyInfo'
import companyAccess from './component/companyAccess'
import contactUs from './component/contactUs'
import verboseUploadFile from '@/components/common/verboseUploadFile'
export default {
  props: ['manager', 'user'],
  components: {
    cover,
    carousel,
    agreedRentPieces,
    agreedRentPieces2,
    agreedSellPieces,
    agreedSellPieces2,
    investSellPieces,
    investSellPieces2,
    popularRentPieces,
    popularRentPieces2,
    popularSellPieces,
    popularSellPieces2,
    recommendRentPieces,
    recommendRentPieces2,
    recommendSellPieces,
    recommendSellPieces2,
    studentRentPieces,
    studentRentPieces2,
    newSellPieces,
    newSellPieces2,
    newRentPieces,
    newRentPieces2,
    services,
    services2,
    topics,
    topicCenter,
    topicCenter2,
    shopInfo,
    shops,
    downloads,
    links,
    navigation,
    sellPieces,
    sellPieces2,
    rentPieces,
    rentPieces2,
    companyInfo,
    companyAccess,
    contactUs,
    verboseUploadFile
  },
  data: () => ({
    menuVisible: false
  }),
  computed: {
    barClass () {
      return 'md-dense ' + this.manager.homepageService.homepage.theme + '-primary-gradient'
    },
    leftClass () {
      return 'md-layout-item md-size-' + this.manager.homepageService.page.left + ' md-small-size-100'
    },
    rightClass () {
      return 'md-layout-item md-size-' + this.manager.homepageService.page.right + ' md-small-size-100'
    }
  },
  methods: {
    uploaded (photo) {
      this.user.logo = photo.file
      this.user.icon = photo.thumbnail
    }
  }
}
</script>

<style scoped lang="scss">
.md-app {
  height: calc(100vh - 200px);
  min-height: 300px;
  border: 1px solid #e8e8e8;
}
@media (max-width: 944px) {
  .md-app {
    height: calc(100vh - 270px);
  }
}
.md-drawer {
  width: 230px;
  max-width: calc(100vw - 125px);
}
.md-toolbar-section-start {
  .resize-picture {
    width: auto;
    height: 72px;
    padding: 4px;
  }
  .md-title {
    width: calc(100% - 72px - 56px);
    color: #fff;
  }
  .md-icon-button {
    position: absolute;
    top: 16px;
    right: 8px;
    .md-icon {
      color: #fff !important;
    }
  }
}
.app-content-auto-height {
  padding: 0px;
}
</style>

<style lang="scss">
.hp-info-title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
  .md-title {
    margin-left: 10px;
  }
}
.hp-title-bar {
  width: 5px;
  height: 30px;
  border-radius: 1px;
}
.hp-title-line {
  width: 100px;
  height: 1px;
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.hp-more {
  position: absolute;
  right: -8px;
  top: 12px;
}
</style>



