import moment from 'moment'

class Topic {
  constructor (data) {
    this._id = data._id || null
    this.user = data.user
    this._type = data.type || null
    this._text = data.text || null
    this._idate = null
    if (data.idate) this._idate = moment(data.idate)
    else this._idate = moment()
    this.isDirty = false
  }
  get type () {
    return this._type
  }
  set type (data) {
    this._type = data
    this.isDirty = true
  }
  get text () {
    return this._text
  }
  set text (data) {
    this._text = data
    this.isDirty = true
  }
  get idate () {
    return this._idate ? this._idate.valueOf() : null
  }
  get idateText () {
    return this._idate != null ? this._idate.format('YYYYMD') : '-'
  }
  set idate (data) {
    if (data) this._idate = moment(data)
    else this._idate = null
    this.isDirty = true
  }

  isValid () {
    if (!this.user) return false
    if (!this.type) {
      this.type = ''
      return false
    }
    if (!this.text) {
      this.text = ''
      return false
    }
    if (!this.idate) return false
    return true
  }

  toJSON () {
    return {
      _id: this._id,
      user: this.user,
      type: this.type,
      text: this.text,
      idate: this.idate
    }
  }
}

export default Topic



