<template>
<div class="app-content-auto-height">
  <vue-headful title=" - "/>
  <div style="position: relative;" v-if="plan">

    <div class="plan-block">
      <div class="wrapper">
        <div class="caption"></div>
        <div class="heading">
          <div class="md-layout plan-row plan-divider">
            <div class="md-layout-item md-size-40">
            </div>
            <div class="md-layout-item md-size-30 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-title">
              {{plan.name}}
            </div>
          </div>
          <div class="md-layout plan-row">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              {{plan.init}}
            </div>
          </div>
          <div class="md-layout plan-row plan-divider">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              {{plan.monthly}}
            </div>
          </div>
          <div class="md-layout plan-row">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              
            </div>
          </div>
          <div class="md-layout plan-row">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              16
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              {{plan.pieces}}
            </div>
          </div>
          <div class="md-layout plan-row">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              {{plan.domain}}
            </div>
          </div>
          <div class="md-layout plan-row">
            <div class="md-layout-item md-size-40 plan-title">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value free">
              
            </div>
            <div class="md-layout-item md-size-30 plan-value">
              {{plan.duration}}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="plan-block">
      <div class="wrapper">
        <div class="caption"></div>
        <div class="heading">
          <p class="md-body-1">30</p>

          <p class="md-title">Step1. </p>
          <p class="md-body-1"></p>
          <div class="md-layout md-gutter" style="padding: 16px;">
            <div class="md-layout-item md-size-60 md-small-size-100">
              <md-field md-clearable :class="{'md-invalid': !isDomainValid}">
                <label for="name"></label>
                <md-input v-model="domain" required></md-input>
                <span class="md-helper-text">www.xxx.com</span>
                <span class="md-error" v-if="domain === '' || (domain && !domain.trim())"></span>
                <span class="md-error" v-else>100</span>
              </md-field>
            </div>
            <div class="md-layout-item md-size-20 md-small-size-50 md-xsmall-size-100">
              <md-datepicker v-model="start">
                <label></label>
              </md-datepicker>
            </div>
            <div class="md-layout-item md-size-20 md-small-size-50 md-xsmall-size-100">
              <md-field>
                <label></label>
                <md-select v-model="duration">
                  <md-option value="1" v-if="plan.name !== 'A' && plan.name !== 'B'"></md-option>
                  <md-option value="2" v-if="plan.name !== 'A' && plan.name !== 'B'"></md-option>
                  <md-option value="3" v-if="plan.name !== 'A' && plan.name !== 'B'"></md-option>
                  <md-option value="6"></md-option>
                  <md-option value="12"></md-option>
                  <md-option value="24"></md-option>
                </md-select>
              </md-field>
            </div>
            <div class="md-layout-item md-size-33 md-small-size-50 md-xsmall-size-100">
              <md-field :class="{'md-invalid': !isCompanyValid}">
                <label></label>
                <md-input v-model="company" type="text" required></md-input>
                <span class="md-error">15</span>
              </md-field>
            </div>
            <div class="md-layout-item md-size-66 md-small-size-50 md-xsmall-size-100">
              <md-field :class="{'md-invalid': !isLicenseValid}">
                <label></label>
                <md-input v-model="license" type="text" required></md-input>
                <span class="md-error">20</span>
              </md-field>
            </div>
            <div class="md-layout-item md-size-50 md-xsmall-size-100">
              <md-field :class="{'md-invalid': !isPhoneValid}">
                <label></label>
                <md-input v-model="phone" type="text" required></md-input>
                <span class="md-error">15</span>
              </md-field>
            </div>
            <div class="md-layout-item md-size-50 md-xsmall-size-100">
              <md-field :class="{'md-invalid': !isEmailValid}">
                <label></label>
                <md-input v-model="email" type="text" required></md-input>
                <span class="md-error">40</span>
              </md-field>
            </div>
            <div class="md-layout-item md-size-100" style="text-align: right;">
              <p class="md-subheading"><a @click="privacy = true"></a><a @click="cautionary = true"></a></p>
              <md-checkbox v-model="aggreement"></md-checkbox>
            </div>
            <div class="md-layout-item md-size-100 action-container">
              <md-button class="md-accent md-raised" :disabled="!isDomainValid || !isCompanyValid || !isLicenseValid || !isPhoneValid || !isEmailValid" @click="purchase">
                <div class="icon-text">
                  <md-icon class="animate-icon">how_to_vote</md-icon><p></p>
                </div>
              </md-button>
            </div>
          </div>

          <p class="md-title">Step2. </p>
          <p class="md-body-1">http://{{manager.user._id}}.budousan.com</p>
          <p class="md-body-1"></p>

          <p class="md-title">Step3. </p>
          <p class="md-body-1"></p>

          <p class="md-title">Step4. </p>
          <p class="md-body-1">15</p>

        </div>
      </div>
    </div>

  </div>

  <md-dialog md-theme="default" :md-active.sync="privacy">
    <md-dialog-content>
      <p class="md-title"></p>
      <p>wyxTech</p>
      <p class="md-body-2">1.</p>
      <p></p>
      <p class="md-body-2">2. </p>
      <p></p>
      <p class="md-body-2">3. </p>
      <p></p>
      <p class="md-body-2">4. </p>
      <p>26</p>
      <p class="md-body-2">5. </p>
      <p></p>
      <p class="md-body-2">6. </p>
      <p></p>
      <p class="md-body-2">7. </p>
      <p></p>
      <p class="md-body-2">8. </p>
      <p></p>
      <p class="md-body-2">9. </p>
      <p></p>
      <p class="md-body-2">10. </p>
      <p></p>
      <p class="md-title"></p>
      <p><md-icon class="md-primary">business</md-icon>wyxTech </p>
      <p><md-icon class="md-primary">phone</md-icon>080-3408-9047</p>
      <p><md-icon class="md-primary">email</md-icon>support@wyxtech.com</p>
      <p><md-icon class="md-primary">room</md-icon>510  14</p>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-accent" @click="privacy = false"></md-button>
    </md-dialog-actions>
  </md-dialog>
  <md-dialog md-theme="default" :md-active.sync="cautionary">
    <md-dialog-content>
      <p class="md-title"></p>
      <p>wyxTech</p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p>Web</p>
      <p></p>
      <p></p>
      <p class="md-title"></p>
      <p><md-icon class="md-primary">business</md-icon>wyxTech</p>
      <p><md-icon class="md-primary">phone</md-icon>03-6807-6486</p>
      <p><md-icon class="md-primary">email</md-icon>budousan@wyxtech.com</p>
      <p><md-icon class="md-primary">room</md-icon>510  14</p>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-accent" @click="cautionary = false"></md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import moment from 'moment'
export default {
  props: ['manager'],
  data: () => ({
    plan: null,
    domain: null,
    start: null,
    duration: '6',
    company: null,
    license: null,
    phone: null,
    email: null,
    aggreement: false,
    plans: {
      a: {
        name: 'A',
        init: '',
        monthly: '1080',
        pieces: '16',
        domain: '',
        duration: ''
      },
      b: {
        name: 'B',
        init: '',
        monthly: '2160',
        pieces: '80',
        domain: '',
        duration: ''
      },
      c: {
        name: 'C',
        init: '3',
        monthly: '3240',
        pieces: '80',
        domain: '',
        duration: ''
      },
      d: {
        name: 'D',
        init: '3',
        monthly: '5400',
        pieces: '200',
        domain: '',
        duration: ''
      }
    },
    privacy: false,
    cautionary: false
  }),
  computed: {
    isDomainValid () {
      if (this.domain === null) return true
      if (!this.domain || !this.domain.trim()) return false
      if (this.domain && this.domain.trim().length > 100) return false
      return true
    },
    isCompanyValid () {
      if (this.company === null) return true
      if (!this.company || !this.company.trim()) return false
      if (this.company && this.company.trim().length > 15) return false
      return true
    },
    isLicenseValid () {
      if (this.license === null) return true
      if (!this.license || !this.license.trim()) return false
      if (this.license && this.license.trim().length > 20) return false
      return true
    },
    isPhoneValid () {
      if (this.phone === null) return true
      if (!this.phone || !this.phone.trim()) return false
      if (this.phone && this.phone.trim().length > 15) return false
      return true
    },
    isEmailValid () {
      if (this.email === null) return true
      if (!this.email || !this.email.trim()) return false
      if (this.email && this.email.trim().length > 40) return false
      return true
    }
  },
  mounted () {
    utils.event.$emit('SCROLL_TO_TOP')
    if (!this.$route.params.plan || ['a', 'b', 'c', 'd'].indexOf(this.$route.params.plan) < 0) {
      utils.event.$emit('SHOW_MESSAGE', {code: 'B009', detail: ''}, () => {
        utils.router.push({path: '/'})
      })
    } else {
      this.plan = this.plans[this.$route.params.plan]
    }
    this.start = new Date()
    if (manager.user.company) this.company = manager.user.company
    if (manager.user.license) this.license = manager.user.license
    if (manager.user.phone) this.phone = manager.user.phone
    if (manager.user.email) this.email = manager.user.email
  },
  methods: {
    purchase () {
      let isValid = true
      if (!this.domain) {
        this.domain = ''
        isValid = false
      }
      if (!this.company) {
        this.company = ''
        isValid = false
      }
      if (!this.license) {
        this.license = ''
        isValid = false
      }
      if (!this.phone) {
        this.phone = ''
        isValid = false
      }
      if (!this.email) {
        this.email = ''
        isValid = false
      }
      if (!moment(this.start).isValid()) {
        this.start = ''
        isValid = false
      }
      if (!isValid) return

      if (!this.aggreement) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B010'})
        return
      }

      let message = {
        domain: this.domain,
        start: moment(this.start).format('YYYYMD'),
        duration: this.duration,
        company: this.company,
        license: this.license,
        phone: this.phone,
        email: this.email
      }
      this.manager.purchaseService.purchase(message, () => {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I013'}, () => {
          utils.router.push({path: '/'})
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
.app-content-auto-height {
  padding: 32px;
}
.plan-block {
  width: 100%;
  padding: 10px 0;
  font-size: 20px;
}
.heading {
  margin: 32px 0;
  font-size: 16px;
  text-align: left;
  .plan-row {
    margin: 8px 200px;
    padding: 8px 0px;
    border-bottom: 1px solid #e8e8e8;
  }
  .plan-divider {
    border-bottom: 2px solid #7a4171;
  }
  .plan-title {
    text-align: right;
    font-weight: 500;
  }
  .plan-value {
    text-align: right;
    color: #7a4171;
  }
  .free {
    color: #888;
    text-decoration: line-through;
  }
  @media (max-width: 944px) {
    .plan-row {
      margin: 8px 64px;
    }
  }
  @media (max-width: 600px) {
    .plan-row {
      margin: 8px 16px;
    }
  }
}
.plan-block .wrapper {
  padding: 5px;
  text-align: center;
  border: 1px solid #7a4171;
  border-top: none;
  width: 100%;
}
.plan-block .wrapper .caption {
  -ms-transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  overflow: hidden;
  margin: -4px -4px 0 -6px;
  position: absolute;
  width: 100%;
  font-size: 18px;
  color: #7a4171;
}
.plan-block .wrapper .caption:before,
.plan-block .wrapper .caption:after {
  content: "";
  position: absolute;
  width: 100%;
  margin-top: -2px;
  border-top: 1px solid #7a4171;
  top: 50%;
}
.plan-block .wrapper .caption:before {
  -ms-transform: translate(-100%, 0);
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100%, 0);
  margin-left: -30px;
}
.plan-block .wrapper .caption:after {
  margin-left: 30px;
}

.heading {
  p {
    padding: 0 16px;
  }
}
.md-subheading {
  a {
    cursor: pointer;
  }
}
.action-container {
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.md-dialog-content {
  p {
    display: flex;
    align-items: center;
    .md-icon {
      font-size: 16px !important;
      margin: 0 8px 0 0;
    }
  }
}
</style>



