class Homepage {
  constructor (data) {
    this._id = data._id
    this.domain = data.domain || null
    this.theme = data.theme || 'theme05'
    this.template = data.template || null
    this.pages = data.pages || null
  }
  toJSON () {
    return {
      _id: this._id,
      domain: this.domain,
      theme: this.theme,
      template: this.template,
      pages: this.pages
    }
  }
}

export default Homepage



