<template>
<div class="file-card">
  <md-card>
    <md-card-header>
      <div class="md-title">
        {{file.title}}
        <md-button md-theme="default" class="md-fab md-mini md-plain md-primary action" @click="editTitle = true">
          <md-icon>edit</md-icon>
        </md-button>
      </div>
    </md-card-header>

    <md-card-content>
      {{file.detail}}
      <md-button md-theme="default" class="md-fab md-mini md-plain md-primary action" @click="editDetail = true">
        <md-icon>edit</md-icon>
      </md-button>
    </md-card-content>

    <md-card-actions md-alignment="space-between">
      <div class="md-caption">{{formatSize(file.size)}}</div>
      <md-button class="md-icon-button md-accent" @click="downloadFile(file)" v-if="file.file"><md-icon>file_download</md-icon></md-button>
    </md-card-actions>
  </md-card>

  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-file" @click="deleteFile(index)">
    <md-icon>close</md-icon>
  </md-button>
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editTitle"
    v-model="file.title"
    md-title=""
    md-input-maxlength="15"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editDetail"
    v-model="file.detail"
    md-title=""
    md-input-maxlength="60"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
</div>
</template>

<script>
import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
export default {
  props: ['manager', 'user', 'file', 'index'],
  data: () => ({
    editTitle: false,
    editDetail: false
  }),
  methods: {
    formatSize (size) {
      return utils.formatSize(size)
    },
    downloadFile (file) {
      utils.downloadFile(file)
    },
    deleteFile (index) {
      this.user.downloads.splice(index, 1)
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  height: 100%;
}
.md-caption {
  padding-left: 10px;
}
.file-card {
  position: relative;
}
.md-title {
  min-height: 32px;
  position: relative;
}
.md-card-content {
  min-height: 60px;
  position: relative;
}
.action {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.delete-file {
  position: absolute;
  top: -25px;
  right: -10px;
}
</style>



