<template>
<div class="wrapper">
  <md-content class="md-primary app-title primary-gradient md-elevation-8">
    <img class="resize-picture" src="/static/favicon.png">
  </md-content>
  <md-card class="md-elevation-8">
    <md-card-header>
      <div class="md-title"></div>
    </md-card-header>

    <md-card-content>
      <md-field md-clearable :class="{'md-invalid': !isAccountValid}">
        <label></label>
        <md-input ref="signin_user" v-model="account"></md-input>
        <span class="md-error">410</span>
      </md-field>
      <md-field md-clearable :class="{'md-invalid': !isPasswordValid}" :md-toggle-password="false">
        <label></label>
        <md-input ref="signin_password" v-model="password" type="password" @keyup.enter.native="signin()"></md-input>
        <span class="md-error">620</span>
      </md-field>
    </md-card-content>

    <md-card-actions>
      <md-button class="md-primary" to="/signup"></md-button>
      <md-button class="md-primary md-raised" to="/"></md-button>
      <md-button class="md-primary md-raised" @click.native="signin()" :disabled="!isAccountValid || !isPasswordValid"></md-button>
    </md-card-actions>
  </md-card>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
export default {
  props: ['manager'],
  data: () => ({
    account: null,
    password: null,
    isAccountValid: true,
    isPasswordValid: true
  }),
  created () {
    manager.signout()
    this.account = null
    this.password = null
    this.isAccountValid = true
    this.isPasswordValid = true
  },
  watch: {
    account () {
      this.$nextTick(() => {
        this.account = this.account.toLowerCase()
      })
      if (this.account !== null && (this.account.length < 4 || this.account.length > 10)) {
        this.isAccountValid = false
      } else if (this.account !== null && this.account.match(/[^a-z0-9]+/)) {
        this.isAccountValid = false
      } else {
        this.isAccountValid = true
      }
    },
    password () {
      if (this.password !== null && (this.password.length < 6 || this.password.length > 20)) {
        this.isPasswordValid = false
      } else if (this.password !== null && this.password.match(/[^A-Za-z0-9]+/)) {
        this.isPasswordValid = false
      } else {
        this.isPasswordValid = true
      }
    }
  },
  methods: {
    signin () {
      const that = this
      const path = that.$route.query.path
      if (!that.account || !that.password) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B004'}, () => {
          if (!that.account) {
            this.$refs.signin_user.$el.focus()
          } else {
            this.$refs.signin_password.$el.focus()
          }
        })
        return
      }
      utils.restPost('/authenticate', {username: that.account, password: that.password}).then(
        response => {
          if (response) {
            manager.signin(response, () => {
              if (path) {
                that.$router.push({path: path})
              } else {
                that.$router.push({path: '/'})
              }
            })
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.md-card {
  width: 350px;
  padding: 10px;
}
.md-card-header {
  text-align: center;
}

.app-title {
  position: absolute;
  left: -500px;
  top: -500px;
  left: calc(50% - 140px);
  top: calc(50% - 180px);
  z-index: 4;
  border-radius: 8px;
  font-weight: 600;
  font-size:24px;
  width: 280px;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  .resize-picture {
    width: auto;
    height: 72px;
  }
}

.md-field + .md-field {
  margin-top: 40px;
}
.md-button + .md-button {
  margin-left: 20px;
}
</style>



