<template>
<div class="topic md-layout md-gutter md-alignment-center">
  <div class="md-layout-item md-size-20 md-small-size-50">
    <md-field md-clearable :class="{'md-invalid': !isTypeValid}">
      <label for="name"></label>
      <md-input v-model="topic.type" required></md-input>
      <span class="md-error" v-if="!topic.type"></span>
      <span class="md-error" v-else>10</span>
    </md-field>
  </div>
  <div class="topic-date md-layout-item md-size-20 md-small-size-50">
    <md-datepicker v-model="idate">
      <label></label>
    </md-datepicker>
  </div>
  <div class="topic-text md-layout-item md-size-50 md-small-size-80 md-xsmall-size-70">
    <md-field md-clearable :class="{'md-invalid': !isTextValid}">
      <label for="name"></label>
      <md-input v-model="topic.text" required></md-input>
      <span class="md-error" v-if="!topic.text"></span>
      <span class="md-error" v-else>100</span>
    </md-field>
  </div>
  <div class="topic-action md-layout-item md-size-10 md-small-size-20 md-xsmall-size-30">
    <md-button class="md-icon-button md-primary" @click="deleteTopic"><md-icon>delete_forever</md-icon></md-button>
    <md-button class="md-icon-button md-primary" @click="saveTopic" :disabled="!isTypeValid || !isTextValid || !topic.isDirty"><md-icon>save</md-icon></md-button>
  </div>
</div>
</template>

<script>
import manager from '@/store/manager.js'
import utils from '@/tool/utils.js'
import moment from 'moment'
export default {
  props: ['manager', 'topic', 'index'],
  data: () => ({
    idate: null
  }),
  computed: {
    isTypeValid () {
      if (this.topic.type === null) return true
      if (this.topic.type && this.topic.type.length > 0 && this.topic.type.length <= 10) return true
      return false
    },
    isTextValid () {
      if (this.topic.text === null) return true
      if (this.topic.text && this.topic.text.length > 0 && this.topic.text.length <= 100) return true
      return false
    }
  },
  created () {
    this.idate = this.topic._idate ? this.topic._idate.format('YYYY-MM-DD') : null
  },
  watch: {
    idate () {
      let idate = moment(this.idate)
      if (idate.isValid() && this.topic.idate !== idate.valueOf()) {
        this.topic.idate = this.idate
      }
    }
  },
  methods: {
    saveTopic () {
      if (!this.topic.isValid()) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B011'})
      } else if (!this.topic._id) {
        manager.topicService.insertTopic(this.topic, () => {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
        })
      } else if (this.topic.isDirty) {
        manager.topicService.updateTopic(this.topic, () => {
          utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
        })
      }
    },
    deleteTopic () {
      if (!this.topic._id) {
        manager.topicService.topics.splice(this.index, 1)
      } else {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I012'}, () => {
          manager.topicService.deleteTopic(this.topic)
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.topic {
  width: 100%;
  margin: 0;
  border-bottom: solid 1px #e8e8e8;
  padding-bottom: 10px;
  margin-bottom: 20px;
}
.topic-type {
  width: 120px;
  padding: 5px 16px;
  border-radius: 20px;
  text-align: center;
}
.md-caption {
  text-align: right;
}
.md-body-1 {
  padding-left: 20px;
  padding-right: 20px;
}
.topic-action {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.delete-topic {
  text-decoration: line-through;
  height: 44px;
  font-size: 16px;
  display: flex;
  align-items: center;
}
</style>



