<template>
<div ref="animate" class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button class="md-accent hp-more" @click="manager.homepageService.switchPage('topics')">
      more
    </md-button>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="component-wrapper">
    <topicLable :manager="manager" :object="topic" v-for="(topic, index) in manager.homepageService.dummyTopics" :key="topic._id" v-if="index < 3"/>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import topicLable from '../shared/topicLable'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    topicLable
  }
}
</script>

<style scoped lang="scss">

</style>



