<template>
<md-card>
  <md-card-header class="ribbon-box">
    <div class="ribbon-area" v-if="object.isAccredited">
      <span class="ribbon primary-gradient primary-active"></span>
    </div>
    <md-avatar>
      <img :src="manager.user.avatar" v-if="object.isExam">
      <img :src="object.avatar" v-else>
    </md-avatar>
    <div class="md-title" v-if="object.isExam">{{manager.user.name}}</div>
    <div class="md-title" v-else>{{object.name}}</div>
    <div class="md-subhead" v-if="object.isExam">{{manager.user.position}}</div>
    <div class="md-subhead" v-else>{{object.position}}</div>
  </md-card-header>

  <md-card-content>
    <div class="contact-detail-logo">
      <img :src="manager.user._logo" v-if="object.isExam && manager.user._logo">
      <img :src="object._logo" v-else-if="!object.isExam && object._logo">
    </div>
    <div class="contact-detail-remark md-elevation-5">
      <p v-if="object.isExam && manager.user.remark">{{manager.user.remark}}</p>
      <p v-else-if="object.isExam && !manager.user.remark"></p>
      <p v-else-if="!object.isExam && object.remark">{{object.remark}}</p>
      <p v-else></p>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">business</md-icon>
      </div>
      <div class="contact-text" v-if="object.company">
        {{object.company}}
      </div>
      <div class="contact-text" v-else>
        
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">call</md-icon>
      </div>
      <div class="contact-text" v-if="object.phone">
        {{object.phone}}
      </div>
      <div class="contact-text" v-else>
        
      </div>
    </div>
    <div class="contact-detail-row" v-if="object.fax">
      <div>
        <md-icon class="md-primary">print</md-icon>
      </div>
      <div class="contact-text">
        {{object.fax}}
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">email</md-icon>
      </div>
      <div class="contact-text" v-if="object.email">
        {{object.email}}
      </div>
      <div class="contact-text" v-else>
        
      </div>
    </div>
    <div class="contact-detail-row" v-if="!object.isExam && object.homepage">
      <div>
        <md-icon class="md-primary">home</md-icon>
      </div>
      <div class="contact-text">
        <a class="homepage" @click="openHomepage">{{object.homepage}}</a>
      </div>
    </div>
    <div class="contact-detail-row" v-else-if="object.isExam && manager.user.homepage">
      <div>
        <md-icon class="md-primary">home</md-icon>
      </div>
      <div class="contact-text">
        <a class="homepage" @click="openHomepage">{{manager.user.homepage}}</a>
      </div>
    </div>
    <div class="contact-detail-row">
      <div>
        <md-icon class="md-primary">room</md-icon>
      </div>
      <div class="contact-text address-text" v-if="object.address">
        {{object.address}}
      </div>
      <div class="contact-text address-text" v-else>
        
      </div>
    </div>
  </md-card-content>

  <md-card-actions>
    <md-content class="md-primary license-badge md-elevation-1" v-if="object.license">
      {{object.license}}
    </md-content>
    <!-- <md-button class="md-icon-button md-accent" @click="openHomepage">
      <md-icon>assignment_ind</md-icon>
    </md-button> -->
    <!-- <md-button class="md-icon-button md-accent" to="/chat" v-if="object._id !== manager.user._id">
      <md-icon>forum</md-icon>
    </md-button> -->
  </md-card-actions>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object'],
  methods: {
    openHomepage () {
      if (this.object.homepage) {
        let url = this.object.homepage
        if (url.indexOf('http') !== 0) {
          url = 'http://' + url
        }
        utils.openInNewTab(url)
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  min-width: 337px;
  max-width: 420px;
  margin: 0px;
}

.ribbon-box {
  position: relative;
  box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
  pointer-events: none;
}
.ribbon {
  display: inline-block;
  position: absolute;
  padding: 7px 0;
  left: -23px;
  top: 22px;
  width: 160px;
  text-align: center;
  font-size: 18px;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: "";
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before {
  left: 14px;
}
.ribbon:after {
  right: 18px;
}

.md-card-content {
  position: relative;
  padding-bottom: 0px;
}
.md-subhead {
  font-size: 12px;
  height: 20px;
}
.contact-detail-logo {
  position: absolute;
  top: -10px;
  left: 36px;
  width: 120px;
  height: 120px;
  opacity: 0.3;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    max-width: 120px;
    max-height: 120px;
  }
}
.company-logo {
  width: 100%;
  height: auto;
}
.contact-detail-remark {
  opacity: 0.7;
  position: absolute;
  top: -15px;
  padding: 8px;
  width: calc(100% - 205px);
  background: #e8e8e8;
  border-radius: 2px;
  p {
    margin: 0;
    font-size: 11px;
  }
  &::before {
    position: absolute;
    top: -8px;
    left: 15px;
    content: '';
    width: 0;
    height: 0;
    font-weight: 100;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    border-bottom: solid 8px #e8e8e8;
  }
}
.contact-detail-row {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 11px;
  margin-top: 8px;
  justify-content: flex-end;
  .md-icon {
    font-size: 16px !important;
  }
}
.contact-text {
  min-width: 140px;
  min-height: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
}
.homepage {
  cursor: pointer;
}
.address-text {
  width: 100%;
}
.md-card-actions {
  height: 56px;
}
.license-badge {
  position: absolute;
  bottom: 15px;
  left: 20px;
  padding: 5px 10px;
  border-radius: 30px;
}
</style>



