<template>
<div v-if="manager.myRentPieceService.rentPiece">
  <vue-headful title=" - "/>
  <div class="md-headline" v-if="!manager.myRentPieceService.rentPiece.id"></div>
  <div class="md-headline" v-else></div>
  <md-steppers :md-active-step.sync="active" md-vertical md-linear>
    <md-step id="step1" md-label="" :md-error="firstStepError" :md-editable="false" :md-done.sync="step1">
      <md-button class="md-raised md-accent" @click="cancel()"></md-button>
      <md-button class="md-raised md-accent" @click="setFirstStepDone()"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
      <myRentPieceBase ref="baseInfo" :manager="manager"/>
      <md-button class="md-raised md-accent" @click="cancel()"></md-button>
      <md-button class="md-raised md-accent" @click="setFirstStepDone()"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
    </md-step>
    <md-step id="step2" md-label="" :md-error="secondStepError" :md-editable="false" :md-done.sync="step2">
      <md-button class="md-raised md-accent" @click="back('step2', 'step1')"></md-button>
      <md-button class="md-raised md-accent" @click="setSecondStepDone()"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
      <myRentPieceLocation ref="locationInfo" :manager="manager"/>
      <md-button class="md-raised md-accent" @click="back('step2', 'step1')"></md-button>
      <md-button class="md-raised md-accent" @click="setSecondStepDone()"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
    </md-step>
    <md-step id="step3" md-label="" :md-editable="false" :md-done.sync="step3">
      <md-button class="md-raised md-accent" @click="back('step3', 'step2')"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
      <myRentPiecePicture ref="pictures" :manager="manager"/>
      <md-button class="md-raised md-accent" @click="back('step3', 'step2')"></md-button>
      <md-button class="md-raised md-accent" @click="save()"></md-button>
    </md-step>
  </md-steppers>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import myRentPieceBase from './myRentPieceBase'
import myRentPieceLocation from './myRentPieceLocation'
import myRentPiecePicture from './myRentPiecePicture'
export default {
  props: ['manager'],
  components: {
    myRentPieceBase,
    myRentPieceLocation,
    myRentPiecePicture
  },
  data: () => ({
    active: 'step1',
    step1: false,
    step2: false,
    step3: false,
    firstStepError: null,
    secondStepError: null
  }),
  methods: {
    cancel () {
      this.$router.push({name: 'myRent'})
    },
    setFirstStepDone () {
      this.firstStepError = this.$refs.baseInfo.validate()
      if (!this.firstStepError) {
        this.step1 = true
        this.active = 'step2'
      } else {
        utils.event.$emit('SCROLL_TO_TOP')
      }
    },
    setSecondStepDone () {
      this.secondStepError = this.$refs.locationInfo.validate()
      if (!this.secondStepError) {
        this.step2 = true
        this.active = 'step3'
      } else {
        utils.event.$emit('SCROLL_TO_TOP')
      }
    },
    back (current, pre) {
      this[current] = false
      this[pre] = false
      this.active = pre
    },
    save () {
      const that = this
      this.step1 = false
      this.step2 = false
      this.step3 = false
      this.firstStepError = this.$refs.baseInfo.validate()
      if (this.firstStepError) {
        utils.event.$emit('SCROLL_TO_TOP')
        this.step1 = false
        this.active = 'step1'
        return
      } else {
        this.step1 = true
      }

      this.secondStepError = this.$refs.locationInfo.validate()
      if (this.secondStepError) {
        utils.event.$emit('SCROLL_TO_TOP')
        this.step2 = false
        this.active = 'step2'
        return
      } else {
        this.step2 = true
      }

      // that.$refs.pictures.upload(() => {
      //   if (!manager.myRentPieceService.rentPiece.id) {
      //     manager.myRentPieceService.insertRentPiece(manager.myRentPieceService.rentPiece, () => {
      //       that.$router.push({path: '/my/rent'})
      //     })
      //   } else {
      //     manager.myRentPieceService.updateRentPiece(manager.myRentPieceService.rentPiece, () => {
      //       that.$router.push({path: '/my/rent'})
      //     })
      //   }
      // })
      that.$refs.pictures.validate()
      if (!manager.myRentPieceService.rentPiece.id) {
        manager.myRentPieceService.insertRentPiece(manager.myRentPieceService.rentPiece, () => {
          that.$router.push({path: '/my/rent'})
        })
      } else {
        manager.myRentPieceService.updateRentPiece(manager.myRentPieceService.rentPiece, () => {
          that.$router.push({path: '/my/rent'})
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-button {
  margin-top: 40px;
  min-width: 70px;
  width: 70px;
}
</style>



