<template>
<md-app md-waterfall md-mode="fixed">
  <vue-headful title=""/>

  <md-app-toolbar class="primary-gradient">
    <div class="md-toolbar-row">
      <div class="md-toolbar-section-start">
        <router-link class="link" to="/">
          <img class="resize-picture" src="/static/favicon.png">
          <p class="md-title"></p>
        </router-link>
        <md-button class="md-icon-button md-accent bd-xsmall-show" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
      </div>
      <div class="md-toolbar-section-end bd-xsmall-hide">
        <md-tabs class="md-accent" :md-active-tab="route">
          <md-tab id="route-top" md-icon="explore" md-label="" to="/"></md-tab>
          <md-tab id="route-sell" md-icon="location_city" md-label="" to="/my/sell"></md-tab>
          <md-tab id="route-rent" md-icon="domain" md-label="" to="/my/rent"></md-tab>
          <md-tab id="route-my" md-icon="accessibility" md-label="" to="/my"></md-tab>
        </md-tabs>
      </div>
    </div>
  </md-app-toolbar>

  <md-app-drawer :md-active.sync="menuVisible">
    <md-toolbar class="md-transparent" md-elevation="0"></md-toolbar>
    <md-list>
      <md-list-item to="/">
        <md-icon>explore</md-icon>
        <span class="md-list-item-text"></span>
      </md-list-item>
      <md-list-item to="/my/sell">
        <md-icon>location_city</md-icon>
        <span class="md-list-item-text"></span>
      </md-list-item>
      <md-list-item to="/my/rent">
        <md-icon>domain</md-icon>
        <span class="md-list-item-text"></span>
      </md-list-item>
      <md-list-item to="/my">
        <md-icon>accessibility</md-icon>
        <span class="md-list-item-text"></span>
      </md-list-item>
    </md-list>
  </md-app-drawer>

  <md-app-content>
    <router-view :manager="manager"/>
    <detailDialog :manager="manager"/>
    <photoViewerDialog :manager="manager"/>
    <tagEditor :manager="manager"/>
  </md-app-content>

</md-app>
</template>

<script>
import utils from '@/tool/utils.js'
import detailDialog from '@/components/shared/detailDialog'
import photoViewerDialog from '@/components/common/photoViewerDialog'
import tagEditor from '@/components/common/tagEditor'
export default {
  props: ['manager'],
  components: {
    detailDialog,
    photoViewerDialog,
    tagEditor
  },
  data: () => ({
    menuVisible: false,
    headerHeight: 72,
    scrollTop: 0,
    timmer: null
  }),
  computed: {
    route () {
      if (this.$route.name === 'top' || this.$route.name === 'purchase') {
        return 'route-top'
      } else if (this.$route.name === 'mySell' || this.$route.name === 'mySellPiece' || this.$route.name === 'mySellPieceDetail' || this.$route.name === 'mySellPiecePreview') {
        return 'route-sell'
      } else if (this.$route.name === 'myRent' || this.$route.name === 'myRentPiece' || this.$route.name === 'myRentPieceDetail' || this.$route.name === 'myRentPiecePreview') {
        return 'route-rent'
      } else if (this.$route.name === 'account' || this.$route.name === 'profile' || this.$route.name === 'apply' || this.$route.name === 'homepage' || this.$route.name === 'inquiry' || this.$route.name === 'topic') {
        return 'route-my'
      }
    }
  },
  mounted () {
    const that = this
    utils.event.$on('SCROLL_TO_TOP', () => {
      that.$nextTick(() => {
        that.scrollTo(0, 300)
      })
    })
    that.scrollTop = 0
    let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
    scrollElement.addEventListener('scroll', this.handleScroll)

    utils.event.$emit('SCROLL_AT', scrollElement.scrollTop + scrollElement.clientHeight + that.headerHeight)
    utils.event.$on('FIRE_SCROLL_AT', () => {
      utils.event.$emit('SCROLL_AT', scrollElement.scrollTop + scrollElement.clientHeight + that.headerHeight)
    })
  },
  beforeDestroy () {
    utils.event.$off('SCROLL_TO_TOP')
    utils.event.$off('FIRE_SCROLL_AT')
    let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
    if (scrollElement) scrollElement.removeEventListener('scroll', this.handleScroll)
  },
  updated () {
    let that = this
    let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
    if (scrollElement) utils.event.$emit('SCROLL_AT', scrollElement.scrollTop + scrollElement.clientHeight + that.headerHeight)
  },
  methods: {
    handleScroll () {
      let scrollElement = document.getElementsByClassName('md-app-scroller')[0]
      if (this.scrollTop < scrollElement.scrollTop && (scrollElement.scrollHeight - scrollElement.scrollTop - scrollElement.clientHeight) < 600) {
        if (this.timmer) clearTimeout(this.timmer)
        this.timmer = setTimeout(() => {
          utils.event.$emit('SCROLL_ON_BOTTOM')
        }, 100)
      }
      if (this.scrollTop < scrollElement.scrollTop) {
        utils.event.$emit('SCROLL_AT', scrollElement.scrollTop + scrollElement.clientHeight + this.headerHeight)
      }
      this.scrollTop = scrollElement.scrollTop
    },
    scrollTo (to, duration) {
      const that = this
      if (duration <= 0) return
      if (!document.getElementsByClassName('md-app-scroller') || document.getElementsByClassName('md-app-scroller').length <= 0) return
      let element = document.getElementsByClassName('md-app-scroller')[0]
      let difference = to - element.scrollTop - element.clientHeight
      let perTick = difference / duration * 10

      setTimeout(() => {
        element.scrollTop = element.scrollTop + perTick
        if (element.scrollTop === to) return
        that.scrollTo(to, duration - 10)
      }, 10)
    }
  }
}
</script>

<style scoped lang="scss">
.md-app {
  width: 100%;
  height: 100%;
  position: relative;
}
.md-drawer {
  width: 230px;
  max-width: calc(100vw - 125px);
}
.md-toolbar-section-start {
  .link {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-decoration: none;
  }
  .md-title {
    color: #fff;
    font-size: 24px;
    font-weight: 600;
  }
  .resize-picture {
    width: auto;
    height: 72px;
    padding: 4px;
  }
  .md-icon-button {
    position: absolute;
    top: 16px;
    right: 8px;
    .md-icon {
      color: #fff !important;
    }
  }
}
</style>



