<template>
<div>
  <p class="md-title">
    
    <verboseUploadFile class="uploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/rentPiece/photos'" :next="photoUploaded" v-if="validPhoto > 0"/>
  </p>
  <div class="md-layout md-alignment-left-top pictures">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(photo, index) in manager.myRentPieceService.rentPiece.photoThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isPhotoDeleted(index)}" :src="photo">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deletePhoto(index)" v-if="!isPhotoDeleted(index)"></md-button>
        <md-button class="delete-button md-primary" @click="recoverPhoto(index)" v-else></md-button>
      </div>
    </div>
  </div>
  <p class="md-title">
    
    <verboseUploadFile class="uploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/rentPiece/floorPlans'" :next="floorPlanUploaded" v-if="validFloorPlan > 0"/>
  </p>
  <div class="md-layout md-alignment-left-top pictures">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(floorPlan, index) in manager.myRentPieceService.rentPiece.floorPlanThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isFloorPlanDeleted(index)}" :src="floorPlan">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deleteFloorPlan(index)" v-if="!isFloorPlanDeleted(index)"></md-button>
        <md-button class="delete-button md-primary" @click="recoverFloorPlan(index)" v-else></md-button>
      </div>
    </div>
  </div>
  <p class="md-title">
    360
    <verboseUploadFile class="uploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/rentPiece/previews'" :next="previewUploaded" v-if="validPreview > 0"/>
  </p>
  <div class="md-layout md-alignment-left-top pictures">
    <div class="md-layout-item md-size-10 md-small-size-20 md-xsmall-size-33 one-picture" v-for="(preview, index) in manager.myRentPieceService.rentPiece.previewThumbnails" :key="index">
      <div class="picture-frame">
        <img :class="{'deleted': isPreviewDeleted(index)}" :src="preview">
      </div>
      <div class="delete-action">
        <md-button class="delete-button md-accent" @click="deletePreview(index)" v-if="!isPreviewDeleted(index)"></md-button>
        <md-button class="delete-button md-primary" @click="recoverPreview(index)" v-else></md-button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import verboseUploadFile from '@/components/common/verboseUploadFile'
export default {
  props: ['manager'],
  components: {
    verboseUploadFile
  },
  data: () => ({
    maximum: 9,
    maximumSmall: 3,
    deletedPhotos: [],
    deletedFloorPlans: [],
    deletedPreviews: []
  }),
  computed: {
    validPhoto () {
      return this.maximum - manager.myRentPieceService.rentPiece.photos.length + this.deletedPhotos.length
    },
    validFloorPlan () {
      return this.maximumSmall - manager.myRentPieceService.rentPiece.floorPlans.length + this.deletedFloorPlans.length
    },
    validPreview () {
      return this.maximumSmall - manager.myRentPieceService.rentPiece.previews.length + this.deletedPreviews.length
    }
  },
  methods: {
    deletePhoto (index) {
      this.deletedPhotos.push(index)
    },
    isPhotoDeleted (index) {
      return this.deletedPhotos.some(one => {
        return one === index
      })
    },
    recoverPhoto (index) {
      for (let i = this.deletedPhotos.length - 1; i >= 0; i--) {
        if (this.deletedPhotos[i] === index) {
          if (manager.myRentPieceService.rentPiece.photos.length - this.deletedPhotos.length >= this.maximum) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximum + ''})
          } else {
            this.deletedPhotos.splice(i, 1)
          }
          break
        }
      }
    },
    deleteFloorPlan (index) {
      this.deletedFloorPlans.push(index)
    },
    isFloorPlanDeleted (index) {
      return this.deletedFloorPlans.some(one => {
        return one === index
      })
    },
    recoverFloorPlan (index) {
      for (let i = this.deletedFloorPlans.length - 1; i >= 0; i--) {
        if (this.deletedFloorPlans[i] === index) {
          if (manager.myRentPieceService.rentPiece.floorPlans.length - this.deletedFloorPlans.length >= this.maximumSmall) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximumSmall + ''})
          } else {
            this.deletedFloorPlans.splice(i, 1)
          }
          break
        }
      }
    },
    deletePreview (index) {
      this.deletedPreviews.push(index)
    },
    isPreviewDeleted (index) {
      return this.deletedPreviews.some(one => {
        return one === index
      })
    },
    recoverPreview (index) {
      for (let i = this.deletedPreviews.length - 1; i >= 0; i--) {
        if (this.deletedPreviews[i] === index) {
          if (manager.myRentPieceService.rentPiece.previews.length - this.deletedPreviews.length >= this.maximumSmall) {
            utils.event.$emit('SHOW_MESSAGE', {code: 'B007', detail: this.maximumSmall + ''})
          } else {
            this.deletedPreviews.splice(i, 1)
          }
          break
        }
      }
    },
    photoUploaded (photo) {
      manager.myRentPieceService.rentPiece.photos.push(photo.file)
      if (photo.thumbnail) {
        manager.myRentPieceService.rentPiece.photoThumbnails.push(photo.thumbnail)
      } else {
        manager.myRentPieceService.rentPiece.photoThumbnails.push(photo.file)
      }
    },
    floorPlanUploaded (floorPlan) {
      manager.myRentPieceService.rentPiece.floorPlans.push(floorPlan.file)
      if (floorPlan.thumbnail) {
        manager.myRentPieceService.rentPiece.floorPlanThumbnails.push(floorPlan.thumbnail)
      } else {
        manager.myRentPieceService.rentPiece.floorPlanThumbnails.push(floorPlan.file)
      }
    },
    previewUploaded (preview) {
      manager.myRentPieceService.rentPiece.previews.push(preview.file)
      if (preview.thumbnail) {
        manager.myRentPieceService.rentPiece.previewThumbnails.push(preview.thumbnail)
      } else {
        manager.myRentPieceService.rentPiece.previewThumbnails.push(preview.file)
      }
    },
    validate () {
      // 
      this.deletedPhotos.sort((left, right) => {
        return (left < right ? 1 : -1)
      })
      this.deletedFloorPlans.sort((left, right) => {
        return (left < right ? 1 : -1)
      })
      this.deletedPreviews.sort((left, right) => {
        return (left < right ? 1 : -1)
      })

      // 
      for (let i = 0; i < this.deletedPhotos.length; i++) {
        manager.myRentPieceService.rentPiece.photos.splice(this.deletedPhotos[i], 1)
        manager.myRentPieceService.rentPiece.photoThumbnails.splice(this.deletedPhotos[i], 1)
      }
      for (let i = 0; i < this.deletedFloorPlans.length; i++) {
        manager.myRentPieceService.rentPiece.floorPlans.splice(this.deletedFloorPlans[i], 1)
        manager.myRentPieceService.rentPiece.floorPlanThumbnails.splice(this.deletedFloorPlans[i], 1)
      }
      for (let i = 0; i < this.deletedPreviews.length; i++) {
        manager.myRentPieceService.rentPiece.previews.splice(this.deletedPreviews[i], 1)
        manager.myRentPieceService.rentPiece.previewThumbnails.splice(this.deletedPreviews[i], 1)
      }

      // 
      this.deletedPhotos.splice(0, this.deletedPhotos.length)
      this.deletedFloorPlans.splice(0, this.deletedFloorPlans.length)
      this.deletedPreviews.splice(0, this.deletedPreviews.length)
    }
  }
}
</script>

<style scoped lang="scss">
.md-title {
  position: relative;
}
.uploader {
  position: absolute;
  top: 0px;
  right: 0px;
}
.one-picture {
  position: relative;
}
.picture-frame {
  margin: 10px 5px 0px 5px;
  height: calc(100% - 10px);
  min-height: 100px;
  background: #e8e8e8;
  .deleted {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: .7;
  }
}
.delete-action {
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% - 10px);
  height: 40px;
  position: absolute;
  bottom: 0px;
  background: rgba(0, 0, 0, .4);
  .delete-button {
    margin: 0;
    width: 100%;
    height: 100%;
    min-width: 40px;
    min-height: 40px;
  }
}
.pictures {
  border: 1px solid #e8e8e8;
  padding-bottom: 10px;
  min-height: 100px;
}
</style>



