import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import template01 from './template/template01'
import template02 from './template/template02'
import template03 from './template/template03'
import template04 from './template/template04'
import template05 from './template/template05'
import template06 from './template/template06'
import template07 from './template/template07'
import template08 from './template/template08'
import template09 from './template/template09'
import template10 from './template/template10'
import template11 from './template/template11'
import template12 from './template/template12'
import template13 from './template/template13'
import template14 from './template/template14'
import template15 from './template/template15'
import template16 from './template/template16'
export default {
  props: ['manager', 'user'],
  components: {
    template01,
    template02,
    template03,
    template04,
    template05,
    template06,
    template07,
    template08,
    template09,
    template10,
    template11,
    template12,
    template13,
    template14,
    template15,
    template16
  },
  methods: {
    useTemplate (template) {
      manager.homepageService.homepage.template = template
      manager.homepageService.homepage.pages = manager.const.template[template]
      if (manager.homepageService.homepage._id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I010', detail: ''}, () => {
          manager.homepageService.updateHomepage(() => {
            manager.homepageService.page = manager.homepageService.homepage.pages[0]
            manager.homepageService.showTemplate = false
          })
        })
      } else {
        manager.homepageService.insertHomepage(() => {
          manager.homepageService.showTemplate = false
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
