<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary switch-config" @click="config.hide = !config.hide">
      <md-icon v-if="!config.hide">visibility_off</md-icon>
      <md-icon v-else>visibility</md-icon>
    </md-button>
    <verboseUploadFile class="file-uploader" ref="fileUploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/resources/banner'" :next="uploaded"/>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div v-if="!config.hide">
    <div class="md-layout" v-if="user.links && user.links.length > 0">
      <div class="md-layout-item md-size-100 banner-frame" v-for="(link, index) in user.links" :key="index">
        <img class="banner" :src="link.img">
        <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-link" @click="deleteLink(index)">
          <md-icon>close</md-icon>
        </md-button>
        <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-link" @click="openLink(link)">
          <md-icon>link</md-icon>
        </md-button>
      </div>
    </div>
  </div>
  <div class="visibility-off" v-else>
    
  </div>

  <md-dialog md-theme="default" :md-active.sync="editLink">
    <md-dialog-title>
      URL
    </md-dialog-title>
    <md-dialog-content>
      <md-field>
        <md-input v-model="url" type="text" maxlength="300" placeholder="Type..."></md-input>
      </md-field>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-accent" @click="editLink = false"></md-button>
      <md-button class="md-accent" @click="setLink()"></md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import verboseUploadFile from '@/components/common/verboseUploadFile'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    verboseUploadFile
  },
  data: () => ({
    editLink: false,
    url: '',
    link: null
  }),
  methods: {
    uploaded (file) {
      if (!this.user.links) this.user.links = []
      this.user.links.push({
        img: file.file,
        url: ''
      })
    },
    deleteLink (index) {
      this.user.links.splice(index, 1)
    },
    openLink (link) {
      this.url = link.url
      this.link = link
      this.$nextTick(() => {
        this.editLink = true
      })
    },
    setLink () {
      if (this.url.indexOf('http') !== 0) this.url = 'http://' + this.url
      this.link.url = this.url
      this.editLink = false
    }
  }
}
</script>

<style scoped lang="scss">
.file-uploader {
  position: absolute;
  top: 12px;
  right: 10px;
}
.md-layout-item {
  margin-bottom: 16px;
}
.banner-frame {
  position: relative;
}
.banner {
  width: 100%;
  object-fit: cover;
}
.delete-link {
  position: absolute;
  top: 0;
  right: 0;
}
.edit-link {
  position: absolute;
  top: 0;
  right: 46px;
}
.switch-config {
  position: absolute;
  top: 6px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.visibility-off {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e8e8e8;
}
</style>



