<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <md-content class="topic-center md-scrollbar">
    <topicCard :manager="manager" :object="topic" v-for="topic in manager.homepageService.dummyTopics" :key="topic._id"/>
  </md-content>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import topicCard from '../shared/topicCard'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    topicCard
  }
}
</script>

<style scoped lang="scss">
.topic-center {
  height: 350px;
  overflow: auto;
  width: 100%;
  padding: 5px;
}
.topic-card + .topic-card {
  margin-top: 10px;
}
</style>



