<template>
<div>
  <vue-headful title=" - "/>
  <div class="md-layout md-gutter md-alignment-center-center">
    <div class="md-layout-item md-size-50 md-small-size-100">
      <md-field>
        <label></label>
        <md-input v-model="manager.homepageService.homepage.domain" type="text" disabled></md-input>
        <md-button class="md-icon-button inline-action" @click="multiDomain = true"><md-icon>more_vert</md-icon></md-button>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-small-size-100 action">
      <md-menu md-size="medium" md-align-trigger>
        <md-button md-menu-trigger :md-theme="manager.homepageService.homepage.theme" class="md-primary md-raised color-sample">
          <span class="color-text"></span>
          <md-chip :md-theme="manager.homepageService.homepage.theme" class="md-accent"></md-chip>
          <md-icon>arrow_drop_down</md-icon>
        </md-button>
        <md-menu-content>
          <md-menu-item @click="changeTheme('theme05')">
            <md-content :md-theme="'theme05'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme05'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme10')">
            <md-content :md-theme="'theme10'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme10'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme15')">
            <md-content :md-theme="'theme15'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme15'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme20')">
            <md-content :md-theme="'theme20'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme20'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme25')">
            <md-content :md-theme="'theme25'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme25'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme30')">
            <md-content :md-theme="'theme30'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme30'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme35')">
            <md-content :md-theme="'theme35'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme35'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme40')">
            <md-content :md-theme="'theme40'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme40'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme45')">
            <md-content :md-theme="'theme45'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme45'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme50')">
            <md-content :md-theme="'theme50'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme50'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme55')">
            <md-content :md-theme="'theme55'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme55'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
          <md-menu-item @click="changeTheme('theme60')">
            <md-content :md-theme="'theme60'" class="md-primary color-sample">
              <strong class="color-text"></strong>
              <md-chip :md-theme="'theme60'" class="md-accent"><strong></strong></md-chip>
            </md-content>
          </md-menu-item>
        </md-menu-content>
      </md-menu>
      <div class="actions">
        <md-button class="md-raised md-accent bd-xsmall-hide" @click="manager.homepageService.showTemplate = true" v-if="!manager.homepageService.showTemplate">
          <div class="icon-text">
            <md-icon>dashboard</md-icon><p></p>
          </div>
        </md-button>
        <md-button class="md-raised md-accent bd-xsmall-hide" @click="manager.homepageService.showTemplate = false" v-if="manager.homepageService.showTemplate && manager.homepageService.homepage.pages && manager.homepageService.homepage.pages.length > 0">
          <div class="icon-text">
            <md-icon>reply</md-icon><p></p>
          </div>
        </md-button>
        <md-button class="md-raised md-accent bd-xsmall-hide" @click="save">
          <div class="icon-text">
            <md-icon>save</md-icon><p></p>
          </div>
        </md-button>
        <md-button class="md-icon-button md-raised md-accent bd-xsmall-show" @click="manager.homepageService.showTemplate = true" v-if="!manager.homepageService.showTemplate">
          <md-icon>dashboard</md-icon>
        </md-button>
        <md-button class="md-icon-button md-raised md-accent bd-xsmall-show" @click="manager.homepageService.showTemplate = false" v-if="manager.homepageService.showTemplate && manager.homepageService.homepage.pages && manager.homepageService.homepage.pages.length > 0">
          <md-icon>reply</md-icon>
        </md-button>
        <md-button class="md-icon-button md-raised md-accent bd-xsmall-show" @click="save">
          <md-icon>save</md-icon>
        </md-button>
      </div>
    </div>
  </div>
  <templates :manager="manager" :user="user" v-if="manager.homepageService.showTemplate"/>
  <preview :manager="manager" :user="user" v-if="!manager.homepageService.showTemplate"/>

  <md-dialog md-theme="default" :md-active.sync="multiDomain">
    <md-dialog-title></md-dialog-title>
    <md-dialog-content>
      <div class="domain-row" v-for="(homepage, index) in manager.homepageService.homepages" :key="'homepage-' + index">
        <md-radio v-model="selected" :value="index" class="md-primary domain-select">{{homepage.domain || ''}}</md-radio>
        <!-- <md-button class="md-icon-button md-primary domain-delete" @click="deleteDomain(homepage)" :disabled="index === selected">
          <md-icon>delete_forever</md-icon>
        </md-button> -->
      </div>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-primary" @click="multiDomain = false"></md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import templates from './templates'
import preview from './preview'
export default {
  props: ['manager'],
  components: {
    templates,
    preview
  },
  data: () => ({
    user: null,
    multiDomain: false,
    selected: 0
  }),
  created () {
    this.user = utils.clone(manager.user)
  },
  watch: {
    selected () {
      manager.homepageService.homepage = manager.homepageService.homepages[this.selected]
      if (!manager.homepageService.homepage.pages || manager.homepageService.homepage.pages.length <= 0) {
        manager.homepageService.showTemplate = true
        manager.homepageService.page = null
      } else {
        manager.homepageService.showTemplate = false
        manager.homepageService.page = manager.homepageService.homepage.pages[0]
      }
    }
  },
  methods: {
    // deleteDomain (homepage) {
    //   const that = this
    //   utils.event.$emit('SHOW_MESSAGE', {code: 'I011', detail: homepage.domain}, () => {
    //     manager.homepageService.deleteHomepage(homepage, () => {
    //       for (let i = 0; i < manager.homepageService.homepages.length; i++) {
    //         if (manager.homepageService.homepages[i]._id === homepage._id) {
    //           manager.homepageService.homepages.splice(i, 1)
    //           if (i < that.selected) that.selected--
    //           break
    //         }
    //       }
    //       utils.event.$emit('SHOW_MESSAGE', {code: 'I007'})
    //     })
    //   })
    // },
    changeTheme (theme) {
      manager.homepageService.homepage.theme = theme
    },
    save () {
      const that = this
      manager.homepageService.updateHomepage(() => {
        utils.restPost('/private/updateUser', that.user.toJSON()).then(
          response => {
            if (response) {
              manager.user.signin(response)
              utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
            }
          }
        )
      })
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout-item {
  margin-bottom: 20px;
}
.inline-action {
  position: absolute;
  top: 8px;
  right: 0px;
}
.action {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.actions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.color-sample {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  font-weight: 500 !important;
  .color-text {
    padding: 2px 6px 0 5px;
    font-size: 13px;
  }
}
.md-chip {
  cursor: pointer;
}
.domains {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.domain-row {
  display: flex;
  align-items: center;
  border-bottom: solid 1px #e8e8e8;
  .domain-select {
    padding: 0px 16px;
    display: flex;
    align-items: center;
    margin: 0px;
    height: 52px;
    width: calc(100% - 52px);
  }
}
.domain-row:hover {
  background: #e8e8e8;
}
.md-radio {
  width: 100% !important;
}
</style>



