  border: none;
}
.app-content-auto-height {
  width: 100%;
  height: auto;
  padding: 16px;
  overflow: hidden;
}
.app-content-fix-height {
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.component-wrapper {
  padding: 16px;
}

// icon and text in one row
.icon-text {
  display: flex;
  justify-content: center;
  align-items: center;
  .md-icon {
    margin-right: 5px;
  }
}

// hover cursor
