import utils from '@/tool/utils.js'
import Inquiry from '@/store/models/inquiry.js'

class InquiryService {
  constructor () {
    this._inquiries = []
  }
  get inquiries () {
    return this._inquiries
  }
  set inquiries (inquiries) {
    if (inquiries) {
      inquiries.forEach(one => {
        this._inquiries.push(new Inquiry(one))
      })
    }
  }

  init () {
    this._inquiries.splice(0, this._inquiries.length)
  }

  findInquiries (next) {
    let that = this
    utils.restGet('/private/findInquiries', {}).then(
      response => {
        if (response) {
          that.inquiries = response
          if (next) next()
        }
      }
    )
  }

  loadInquiries = (to, from, next) => {
    this.init()
    this.findInquiries(next)
  }

  updateInquiry (inquiry, next) {
    utils.restPost('/private/updateInquiry', inquiry).then(saved => {
      if (saved) {
        if (next) next()
      }
    })
  }

  deleteInquiry (inquiry, next) {
    let that = this
    utils.restPost('/private/deleteInquiry', {_id: inquiry._id}).then(saved => {
      if (saved) {
        for (let i = that.inquiries.length - 1; i >= 0; i--) {
          if (that.inquiries[i]._id === inquiry._id) {
            that.inquiries.splice(i, 1)
            break
          }
        }
        if (next) next()
        utils.event.$emit('SHOW_MESSAGE', {code: 'I007'})
      }
    })
  }
}

export default new InquiryService()



