<template>
<div>
  <vue-headful title=" - "/>
  <div class="md-layout md-gutter md-alignment-top">
    <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100">
      <div class="row">
        <contactCard :manager="manager" :object="user"/>
      </div>
    </div>
    <div class="md-layout-item md-size-70 md-medium-size-60 md-small-size-100 md-layout md-gutter">
      <div class="md-layout-item md-size-50">
        <div class="upload-frame">
          <md-content class="md-primary upload-header"></md-content>
          <uploadThumbnail ref="avatarUploader" :manager="manager" :preview="user.avatar" :subFolder="user._id + '/profile'"/>
        </div>
      </div>
      <div class="md-layout-item md-size-50">
        <div class="upload-frame">
          <md-content class="md-primary upload-header"></md-content>
          <uploadThumbnail ref="logoUploader" :manager="manager" :preview="user.logo" type="icon" :subFolder="user._id + '/profile'"/>
        </div>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isNicknameValid}">
          <label></label>
          <md-input v-model="user.nickname" type="text"></md-input>
          <span class="md-error">10</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isPositionValid}">
          <label></label>
          <md-input v-model="user.position" type="text"></md-input>
          <span class="md-error">10</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field>
          <label></label>
          <md-input v-model="user.homepage" type="text" disabled></md-input>
          <md-button class="md-icon-button inline-action" @click="multiDomain = true"><md-icon>more_vert</md-icon></md-button>
        </md-field>
      </div>
      <div class="md-layout-item md-size-100 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isRemarkValid}">
          <label></label>
          <md-input v-model="user.remark" type="text"></md-input>
          <span class="md-error">30</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isCompanyValid}">
          <label></label>
          <md-input v-model="user.company" type="text" :disabled="user.isAccredited"></md-input>
          <span class="md-error">15</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-66 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isLicenseValid}">
          <label></label>
          <md-input v-model="user.license" type="text" :disabled="user.isAccredited"></md-input>
          <span class="md-error">20</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isPhoneValid}">
          <label></label>
          <md-input v-model="user.phone" type="text"></md-input>
          <span class="md-error">15</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isFaxValid}">
          <label>FAX</label>
          <md-input v-model="user.fax" type="text"></md-input>
          <span class="md-error">15</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
        <md-field :class="{'md-invalid': !isEmailValid}">
          <label></label>
          <md-input v-model="user.email" type="text"></md-input>
          <span class="md-error">40</span>
        </md-field>
      </div>
      <div class="md-layout-item md-size-100">
        <md-field :class="{'md-invalid': !isAddressValid}">
          <label></label>
          <md-input v-model="user.address" type="text"></md-input>
          <span class="md-error">60</span>
        </md-field>
      </div>
    </div>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-50 action-container">
      <md-button class="md-accent md-raised action-button" @click="changePassword">
        <div class="icon-text">
          <md-icon>lock</md-icon><p></p>
        </div>
      </md-button>
    </div>
    <div class="md-layout-item md-size-50 action-container">
      <md-button class="md-accent md-raised action-button" @click="save" :disabled="!isNicknameValid || !isCompanyValid || !isPositionValid || !isPhoneValid || !isFaxValid || !isEmailValid || !isLicenseValid || !isRemarkValid || !isAddressValid">
        <div class="icon-text">
          <md-icon>save</md-icon><p></p>
        </div>
      </md-button>
    </div>
  </div>

  <md-dialog md-theme="default" :md-active.sync="multiDomain">
    <md-dialog-title></md-dialog-title>
    <md-dialog-content>
      <div class="domain-row" v-for="(homepage, index) in manager.user.homepages" :key="'homepage-' + index">
        <md-radio v-model="user.homepage" :value="homepage" class="md-primary domain-select">{{homepage}}</md-radio>
      </div>
      <div class="domain-row">
        <md-radio v-model="user.homepage" :value="''" class="md-primary domain-select"></md-radio>
      </div>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-primary" @click="multiDomain = false"></md-button>
    </md-dialog-actions>
  </md-dialog>
  <passwordDialog ref="changePassword" :manager="manager"/>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import contactCard from '@/components/shared/contactCard'
import uploadThumbnail from '@/components/common/uploadThumbnail'
import passwordDialog from './passwordDialog'
export default {
  props: ['manager'],
  components: {
    contactCard,
    uploadThumbnail,
    passwordDialog
  },
  data: () => ({
    multiDomain: false,
    user: null
  }),
  created () {
    this.user = utils.clone(manager.user)
  },
  computed: {
    isNicknameValid () {
      return this.user.nickname.length <= 10
    },
    isCompanyValid () {
      return this.user.company.length <= 15
    },
    isPositionValid () {
      return this.user.position.length <= 10
    },
    isPhoneValid () {
      return this.user.phone.length <= 15
    },
    isFaxValid () {
      return this.user.fax.length <= 15
    },
    isEmailValid () {
      return this.user.email.length <= 40
    },
    isLicenseValid () {
      return this.user.license.length <= 20
    },
    isRemarkValid () {
      return this.user.remark.length <= 30
    },
    isAddressValid () {
      return this.user.address.length <= 60
    }
  },
  methods: {
    save () {
      let that = this
      that.$refs.avatarUploader.upload((avatar) => {
        if (avatar) that.user.avatar = avatar.file
        that.$refs.logoUploader.upload((logo) => {
          if (logo) {
            that.user.logo = logo.file
            that.user.icon = logo.thumbnail
          }
          utils.restPost('/private/updateUser', that.user.toJSON()).then(
            response => {
              if (response) {
                manager.user.signin(response)
                utils.event.$emit('SHOW_MESSAGE', {code: 'I002'})
              }
            }
          )
        })
      })
    },
    changePassword () {
      this.$refs.changePassword.show()
    }
  }
}
</script>

<style scoped lang="scss">
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.triangle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  .md-icon {
    font-size: 60px !important;
  }
}
.upload-frame {
  width: 160px;
  left: 0px;
  right: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.upload-header {
  padding: 5px;
  text-align: center;
}
.md-layout-item {
  margin-top: 10px;
}
.action-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  .action-button {
    width: 150px;
  }
}

.inline-action {
  position: absolute;
  top: 8px;
  right: 0px;
}
.domains {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.domain-row {
  display: flex;
  align-items: center;
  border-bottom: solid 1px #e8e8e8;
  .domain-select {
    padding: 0px 16px;
    display: flex;
    align-items: center;
    margin: 0px;
    height: 52px;
    width: calc(100% - 52px);
  }
}
.domain-row:hover {
  background: #e8e8e8;
}
.md-radio {
  width: 100% !important;
}
</style>



