import manager from '@/store/manager.js'

class User {
  constructor (data) {
    this._id = ''
    this.password = ''
    this.isExam = null
    this.exam = null

    this._avatar = null
    this.nickname = ''
    this.position = ''
    this.remark = ''
    this.registration = null
    this.card = null

    // 0: ; 1: ; 2: ; 3: 
    this.role = '0'

    // homepage
    this.homepage = ''
    this.homepages = []
    this._logo = null
    this.icon = null
    this.company = ''
    this.president = ''
    this.create = ''
    this.capital = ''
    this.license = ''
    this.phone = ''
    this.fax = ''
    this.email = ''
    this.address = ''
    this.access = ''
    this.business = ''
    this.holiday = ''
    this._latitude = null
    this._longitude = null
    this.vision = null
    this.services = null
    this.shops = null
    this.downloads = null
    this.links = null

    if (data) this.signin(data)
  }
  get avatar () {
    return this._avatar || '/static/avatar.png'
  }
  set avatar (data) {
    this._avatar = data || null
  }
  get logo () {
    return this._logo || '/static/noimage.png'
  }
  set logo (data) {
    this._logo = data || null
  }
  get isSignin () {
    return !(this._id === '')
  }
  get isAnonymous () {
    return this._id === ''
  }
  get isOrdinary () {
    return this._id && this.role === '1'
  }
  get isMediator () {
    return this._id && this.role >= '2'
  }
  get isAccredited () {
    return this._id && this.role === '3'
  }
  get name () {
    if (this.nickname) return this.nickname
    else return this._id
  }
  get latitude () {
    return this._latitude == null ? null : parseFloat(this._latitude)
  }
  set latitude (data) {
    if (data) this._latitude = parseFloat(data)
    else this._latitude = null
  }
  get longitude () {
    return this._longitude == null ? null : parseFloat(this._longitude)
  }
  set longitude (data) {
    if (data) this._longitude = parseFloat(data)
    else this._longitude = null
  }
  signin (data) {
    if (data._id != null) this._id = data._id
    if (data.password != null) this.password = data.password
    if (data.isExam != null) this.isExam = data.isExam
    if (data.exam != null) {
      data.exam.isExam = true
      this.exam = new User(data.exam)
    }

    if (data.avatar != null) this.avatar = data.avatar
    if (data.nickname != null) this.nickname = data.nickname
    if (data.position != null) this.position = data.position
    if (data.remark != null) this.remark = data.remark
    if (data.registration != null) this.registration = data.registration
    if (data.card != null) this.card = data.card

    if (data.role != null) this.role = data.role

    if (data.homepage != null) this.homepage = data.homepage
    if (data.homepages != null) this.homepages = data.homepages
    if (data.logo != null) this.logo = data.logo
    if (data.icon != null) this.icon = data.icon
    if (data.company != null) this.company = data.company
    if (data.license != null) this.license = data.license
    if (data.phone != null) this.phone = data.phone
    if (data.fax != null) this.fax = data.fax
    if (data.email != null) this.email = data.email
    if (data.address != null) this.address = data.address
    if (data.access != null) this.access = data.access
    if (data.business != null) this.business = data.business
    if (data.holiday != null) this.holiday = data.holiday
    if (data.latitude != null) this.latitude = data.latitude
    if (data.longitude != null) this.longitude = data.longitude
    if (data.vision != null) {
      this.vision = {}
      this.vision.title = data.vision.title || ''
      this.vision.description = data.vision.description || ''
    } else {
      this.vision = {
        title: '',
        description: ''
      }
    }
    if (data.services != null) {
      this.services = data.services
    } else {
      this.services = []
      this.services.push(manager.const.service['buy'])
      this.services.push(manager.const.service['rent'])
    }
    if (data.shops != null) this.shops = data.shops
    if (data.downloads != null) this.downloads = data.downloads
    if (data.links != null) this.links = data.links
  }
  signout () {
    this._id = ''
    this.password = ''
    this.isExam = null
    this.exam = null
    this.avatar = null
    this.nickname = ''
    this.position = ''
    this.remark = ''
    this.registration = null
    this.card = null

    this.role = '0'

    this.homepage = ''
    this.homepages = []
    this.logo = null
    this.icon = null
    this.company = ''
    this.license = ''
    this.phone = ''
    this.fax = ''
    this.email = ''
    this.address = ''
    this.access = ''
    this.business = ''
    this.holiday = ''
    this.latitude = null
    this.longitude = null
    this.vision = null
    this.services = null
    this.shops = null
    this.downloads = null
    this.links = null
  }
  addShop () {
    if (!this.shops) this.shops = []
    if (this.shops.length > 0) {
      this.shops.push({
        name: '',
        address: '',
        access: '',
        phone: '',
        fax: '',
        email: '',
        latitude: null,
        longitude: null
      })
    } else {
      this.shops.push({
        name: this.company,
        address: this.address,
        access: this.access,
        phone: this.phone,
        fax: this.fax,
        email: this.email,
        latitude: this.latitude,
        longitude: this.longitude
      })
    }
  }
  toJSON () {
    if (this.role === '0') return null
    return {
      _id: this._id,
      // password: this.password,
      exam: this.exam ? this.exam.toJSON() : null,
      avatar: this._avatar,
      nickname: this.nickname,
      position: this.position,
      remark: this.remark,
      registration: this.registration,
      card: this.card,
      role: this.role,
      homepage: this.homepage,
      homepages: this.homepages,
      logo: this._logo,
      icon: this.icon,
      company: this.company,
      license: this.license,
      phone: this.phone,
      fax: this.fax,
      email: this.email,
      address: this.address,
      access: this.access,
      business: this.business,
      holiday: this.holiday,
      latitude: this.latitude,
      longitude: this.longitude,
      vision: this.vision,
      services: this.services,
      shops: this.shops,
      downloads: this.downloads,
      links: this.links
    }
  }
}

export default new User()



