import ChatMessage from './chatMessage.js'

class ChatRoom {
  constructor (data) {
    this._id = data._id
    this.name = data.name
    this.avatar = '/static/user.jpg'
    this.chats = []

    let tmp = [
      {_id: '1', avatar: '/static/user.jpg', text: '', user: 'rrrr'},
      {_id: '2', avatar: '/static/user.jpg', text: '2', user: 'sss'},
      {_id: '3', avatar: '/static/user.jpg', text: '3', user: 'rrrr'},
      {_id: '4', avatar: '/static/user.jpg', text: '444444444444', user: 'kkkk'},
      {_id: '5', avatar: '/static/user.jpg', text: '5', user: 'rrrr'},
      {_id: '6', avatar: '/static/user.jpg', text: '6666', user: 'ttt'},
      {_id: '7', avatar: '/static/user.jpg', text: '7', user: 'rrrr'},
      {_id: '8', avatar: '/static/user.jpg', text: '88', user: 'rrrr'},
      {_id: '9', avatar: '/static/user.jpg', text: '9', user: 'mmmm'},
      {_id: '10', avatar: '/static/user.jpg', text: '10', user: 'rrrr'},
      {_id: '11', avatar: '/static/user.jpg', text: '11111111111111', user: 'rrrr'}
    ]
    tmp.forEach(one => {
      this.chat(one)
    })
  }
  chat (chat) {
    chat.chatTime = new Date()
    this.chats.push(new ChatMessage(chat))
  }
}

export default ChatRoom



