<template>
<div>
  <vue-headful title=" - "/>
  <div class="md-layout md-gutter md-alignment-top">
    <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100">
      <div class="row">
        <contactCard :manager="manager" :object="manager.user"/>
      </div>
      <div class="row">
        <md-button class="md-accent md-raised action-button" to="/my/profile">
          <div class="icon-text">
            <md-icon>edit</md-icon><p></p>
          </div>
        </md-button>
        <md-button class="md-accent md-raised action-button" @click="signout()">
          <div class="icon-text">
            <md-icon>power_settings_new</md-icon><p></p>
          </div>
        </md-button>
      </div>
    </div>
    <div class="md-layout-item md-size-70 md-medium-size-60 md-small-size-100">
      <p class="md-title"></p>
      <md-divider></md-divider>
      <p class="md-body-1"></p>
      <!-- <p class="md-body-2"></p>
      <p class="md-body-1" style="margin-top: 24px;">1. </p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p>
      <p class="md-body-1" style="margin-top: 24px;">2. </p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon></p> -->
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/homepage">
          <div class="icon-text">
            <md-icon>settings</md-icon><p></p>
          </div>
        </md-button>
      </div>
      <p class="md-title"></p>
      <md-divider></md-divider>
      <p class="md-body-1"></p>
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/topic">
          <div class="icon-text">
            <md-icon>new_releases</md-icon><p></p>
          </div>
        </md-button>
      </div>
      <p class="md-title"></p>
      <md-divider></md-divider>
      <p class="md-body-1"></p>
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/inquiry">
          <div class="icon-text">
            <md-icon>forum</md-icon><p></p>
          </div>
        </md-button>
      </div>
      <div v-if="manager.user.homepages && manager.user.homepages.length > 0">
        <p class="md-title"></p>
        <md-divider></md-divider>
        <p class="md-body-1"></p>
        <p><a @click="openHomepage">{{manager.user._id}}.budousan.com</a></p>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import contactCard from '@/components/shared/contactCard'
export default {
  props: ['manager'],
  components: {
    contactCard
  },
  methods: {
    openHomepage () {
      if (process.env.NODE_ENV === 'production') {
        let url = 'http://' + manager.user._id + '.budousan.com'
        utils.openInNewTab(url)
      } else {
        let url = 'http://localhost:8001'
        utils.openInNewTab(url)
      }
    },
    signout () {
      utils.restGet('/logout').then(
        response => {
          if (response) {
            location.href = location.protocol + '//' + location.host + '/'
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.action-button {
  width: 150px;
}
.actions {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.indent {
  padding-left: 16px;
  display: flex;
  align-items: center;
  .md-icon {
    margin: 0;
  }
}
a {
  cursor: pointer;
}
</style>



