<template>
<md-card md-with-hover>
  <md-card-header class="ribbon-box">
    <div class="ribbon-area" v-if="inquiry.finished">
      <span class="ribbon primary-gradient primary-active"></span>
    </div>
    <div class="md-title">{{inquiry.name}}</div>
    <div class="md-subhead">{{inquiry.contact}}</div>
  </md-card-header>

  <md-card-content>
    <div class="inquiry-content">
      {{inquiry.content}}
    </div>
    <div class="inquiry-date">
      {{inquiry.cdateText}}
    </div>
  </md-card-content>

  <md-card-actions>
    <md-button class="md-accent" v-if="!inquiry.finished" @click="finishInquiry(inquiry)">
      
    </md-button>
    <md-button class="md-accent" v-else @click="unfinishInquiry(inquiry)">
      
    </md-button>
  </md-card-actions>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
export default {
  props: ['manager', 'inquiry'],
  methods: {
    finishInquiry (inquiry) {
      let json = {
        _id: inquiry._id,
        finished: true
      }
      manager.inquiryService.updateInquiry(json, () => {
        manager.inquiryService.loadInquiries(null, null, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
      })
    },
    unfinishInquiry (inquiry) {
      let json = {
        _id: inquiry._id,
        finished: false
      }
      manager.inquiryService.updateInquiry(json, () => {
        manager.inquiryService.loadInquiries(null, null, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
  height: 100%;
}

.ribbon-box {
  position: relative;
  box-sizing: border-box;
}
.ribbon-area {
  position: absolute;
  top: -6px;
  right: -6px;
  width: 89px;
  height: 91px;
  overflow: hidden;
  pointer-events: none;
}
.ribbon {
  display: inline-block;
  position: absolute;
  padding: 7px 0;
  left: -23px;
  top: 22px;
  width: 160px;
  text-align: center;
  font-size: 18px;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.ribbon:before, .ribbon:after {
  position: absolute;
  content: "";
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  bottom: -4px;
}
.ribbon:before {
  left: 14px;
}
.ribbon:after {
  right: 18px;
}

.md-title {
  height: 32px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.md-subhead {
  height: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.inquiry-content {
  min-height: 82px;
}
.inquiry-date {
  display: flex;
  justify-content: flex-end;
}
</style>



