<template>
<div class="service-wrapper">
  <md-card :class="cardClass">
    <md-card-header>
      <md-card-header-text>
        <div class="md-title">{{object.title}}</div>
        <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-title" @click="editTitle = true" v-if="edit">
          <md-icon>edit</md-icon>
        </md-button>
        <div class="md-subhead">
          {{object.description}}
        </div>
        <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-service" @click="editService = true" v-if="edit">
          <md-icon>edit</md-icon>
        </md-button>
      </md-card-header-text>

      <md-card-media>
        <md-icon class="md-accent">{{object.icon}}</md-icon>
      </md-card-media>
    </md-card-header>
  </md-card>

  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary add-service" @click="addService()" v-if="!edit">
    <md-icon>add</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-service" @click="deleteService(index)" v-if="edit">
    <md-icon>close</md-icon>
  </md-button>
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editTitle"
    v-model="object.title"
    md-title=""
    md-input-maxlength="15"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editService"
    v-model="object.description"
    md-title=""
    md-input-maxlength="200"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
</div>
</template>

<script>
// import manager from '@/store/manager.js'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    user: {
      default: 'user'
    },
    object: {
      default: 'object'
    },
    edit: {
      type: Boolean,
      default: true
    },
    index: {
      default: null
    }
  },
  computed: {
    cardClass () {
      if (!this.manager.homepageService.homepage.theme || this.manager.homepageService.homepage.theme === 'default') return 'md-primary'
      return this.manager.homepageService.homepage.theme + '-primary-gradient'
    }
  },
  data: () => ({
    editTitle: false,
    editService: false
  }),
  methods: {
    addService () {
      if (!this.user.services) this.user.services = []
      this.user.services.push(this.object)
    },
    deleteService (index) {
      this.user.services.splice(index, 1)
    }
  }
}
</script>

<style scoped lang="scss">
.service-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.md-card {
  height: 100%;
}
.md-card-media {
  .md-icon {
    width: 80px;
    height: 80px;
    font-size: 60px !important;
  }
}
.md-title {
  height: 32px;
}
.delete-service {
  position: absolute;
  top: 0;
  right: 16px;
}
.add-service {
  position: absolute;
  bottom: 0;
  right: 16px;
}
.edit-title {
  position: absolute;
  top: 20px;
  right: 92px;
}
.edit-service {
  position: absolute;
  bottom: 8px;
  right: 96px;
}
</style>



