import utils from '@/tool/utils.js'
import Topic from '@/store/models/topic.js'

class TopicService {
  constructor () {
    this._topics = []
  }
  get topics () {
    return this._topics
  }
  set topics (topics) {
    if (topics) {
      topics.forEach(one => {
        this._topics.push(new Topic(one))
      })
    }
  }

  init () {
    this._topics.splice(0, this._topics.length)
  }

  findTopics (next) {
    let that = this
    utils.restGet('/private/findTopics', {}).then(
      response => {
        if (response) {
          that.topics = response
          if (next) next()
        }
      }
    )
  }

  loadTopics = (to, from, next) => {
    this.init()
    this.findTopics(next)
  }

  insertTopic (topic, next) {
    utils.restPut('/private/insertTopic', topic).then(saved => {
      if (saved) {
        topic._id = saved._id
        topic.isDirty = false
        if (next) next()
      }
    })
  }

  updateTopic (topic, next) {
    utils.restPost('/private/updateTopic', topic).then(saved => {
      if (saved) {
        topic.isDirty = false
        if (next) next()
      }
    })
  }

  deleteTopic (topic, next) {
    let that = this
    utils.restPost('/private/deleteTopic', {_id: topic._id}).then(saved => {
      if (saved) {
        for (let i = that.topics.length - 1; i >= 0; i--) {
          if (that.topics[i]._id === topic._id) {
            that.topics.splice(i, 1)
            break
          }
        }
        if (next) next()
        utils.event.$emit('SHOW_MESSAGE', {code: 'I007'})
      }
    })
  }
}

export default new TopicService()



