import authority from './authority.json'
import aspect from './aspect.json'
import available from './available.json'
import availableRent from './availableRent.json'
import direction from './direction.json'
import layout from './layout.json'
import message from './message.json'
import rights from './rights.json'
import station from './station.json'
import structure from './structure.json'
import type from './type.json'
import typeRent from './typeRent.json'
import emotion from './emotion.json'
import service from './service.json'

import template01 from './template/template01.json'
import template02 from './template/template02.json'
import template03 from './template/template03.json'
import template04 from './template/template04.json'
import template05 from './template/template05.json'
import template06 from './template/template06.json'
import template07 from './template/template07.json'
import template08 from './template/template08.json'
import template09 from './template/template09.json'
import template10 from './template/template10.json'
import template11 from './template/template11.json'
import template12 from './template/template12.json'
import template13 from './template/template13.json'
import template14 from './template/template14.json'
import template15 from './template/template15.json'
import template16 from './template/template16.json'

class Const {
  constructor () {
    this.authority = authority
    this.aspect = aspect
    this.available = available
    this.availableRent = availableRent
    this.direction = direction
    this.layout = layout
    this.message = message
    this.rights = rights
    this.station = station
    this.structure = structure
    this.type = type
    this.typeRent = typeRent
    this.emotion = emotion
    this.service = service

    this.template = {}
    this.template = Object.assign(
      template01,
      template02,
      template03,
      template04,
      template05,
      template06,
      template07,
      template08,
      template09,
      template10,
      template11,
      template12,
      template13,
      template14,
      template15,
      template16
    )

    this.sellTags = [
      '',
      '',
      '',
      '',
      ''
    ]
    this.rentTags = [
      '',
      '',
      '',
      '',
      ''
    ]
  }
}

export default new Const()



