<script>
import utils from '@/tool/utils.js'
import anime from 'animejs'
export default {
  props: ['manager'],
  data: () => ({
    title: '',
    sub1: '',
    sub2: '',
    showTitle: false
  }),
  mounted () {
    const that = this
    utils.event.$on('SCROLL_AT', (position) => {
      if (!that.showTitle && that.$refs.title && position >= that.$refs.title.offsetTop) {
        that.showTitle = true
        anime({
          targets: that.$refs.title.getElementsByClassName('animate1'),
          opacity: [0, 1],
          scale: [0, 1],
          easing: 'easeInOutBack',
          duration: 2500,
          delay: (el, i, l) => {
            return i * 200
          }
        })
        anime({
          targets: that.$refs.title.getElementsByClassName('animate2'),
          opacity: [0, 1],
          translateX: [-100, 0],
          easing: 'easeInOutBack',
          duration: 1500,
          delay: (el, i, l) => {
            return i * 50
          }
        })
        anime({
          targets: that.$refs.title.getElementsByClassName('animate3'),
          opacity: [0, 1],
          translateX: [-100, 0],
          easing: 'easeInOutBack',
          duration: 1500,
          delay: (el, i, l) => {
            return i * 50
          }
        })
      }
    })
  },
  beforeDestroy () {
    utils.event.$off('SCROLL_AT')
  }
}
</script>

