<template>
<div>
  <vue-headful title=" - "/>
  <md-content class="md-scrollbar content-block md-elevation-1">
    <p class="md-title"></p>
    <p></p>
    <p><md-icon class="md-primary">radio_button_unchecked</md-icon></p>
    <p><md-icon class="md-primary">radio_button_unchecked</md-icon></p>
    <p class="md-title"></p>
    <p></p>
    <p class="md-title"></p>
    <p>wyxTech</p>
    <p></p>
    <p></p>
    <p></p>
    <p></p>
    <p>Web</p>
    <p></p>
    <p></p>
    <p class="md-title"></p>
    <p><md-icon class="md-primary">business</md-icon>wyxTech</p>
    <p><md-icon class="md-primary">phone</md-icon>030-9999-3527</p>
    <p><md-icon class="md-primary">email</md-icon>support@wyxtech.com</p>
    <p><md-icon class="md-primary">room</md-icon>510  14</p>
  </md-content>
  <md-content :class="{'aggreement': true, 'md-accent': !aggreement}">
    <md-checkbox v-model="aggreement"></md-checkbox>
  </md-content>

  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isCompanyValid}">
        <label></label>
        <md-input v-model="user.company" type="text" required></md-input>
        <span class="md-error">15</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isPhoneValid}">
        <label></label>
        <md-input v-model="user.phone" type="text" required></md-input>
        <span class="md-error">15</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33 md-medium-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isFaxValid}">
        <label>FAX</label>
        <md-input v-model="user.fax" type="text"></md-input>
        <span class="md-error">15</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <md-field :class="{'md-invalid': !isEmailValid}">
        <label></label>
        <md-input v-model="user.email" type="text" required></md-input>
        <span class="md-error">40</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-medium-size-100">
      <md-field :class="{'md-invalid': !isLicenseValid}">
        <label></label>
        <md-input v-model="user.license" type="text" required></md-input>
        <span class="md-error">20</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-100">
      <md-field :class="{'md-invalid': !isAddressValid}">
        <label></label>
        <md-input v-model="user.address" type="text" required></md-input>
        <span class="md-error">60</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <div class="upload-frame">
        <md-content class="md-primary upload-header"></md-content>
        <uploadThumbnail ref="registrationUploader" :manager="manager" :preview="user.registration" :subFolder="user._id + '/apply'"/>
      </div>
    </div>
    <div class="md-layout-item md-size-50 md-xsmall-size-100">
      <div class="upload-frame">
        <md-content class="md-primary upload-header"></md-content>
        <uploadThumbnail ref="cardUploader" :manager="manager" :preview="user.card" :subFolder="user._id + '/apply'"/>
      </div>
    </div>
    <div class="md-layout-item md-size-100 action-container">
      <md-button class="md-accent md-raised action-button" @click="exam()" :disabled="!isCompanyValid || !isPhoneValid || !isFaxValid || !isEmailValid || !isLicenseValid || !isAddressValid">
        <div class="icon-text">
          <md-icon>security</md-icon><p></p>
        </div>
      </md-button>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import uploadThumbnail from '@/components/common/uploadThumbnail'
export default {
  props: ['manager'],
  components: {
    uploadThumbnail
  },
  data: () => ({
    user: null,
    aggreement: false
  }),
  created () {
    if (manager.user.exam) {
      this.user = utils.clone(manager.user.exam)
    } else {
      this.user = utils.clone(manager.user)
    }
  },
  computed: {
    isCompanyValid () {
      if (!this.user.company) return false
      return this.user.company.length <= 15
    },
    isPhoneValid () {
      if (!this.user.phone) return false
      return this.user.phone.length <= 15
    },
    isFaxValid () {
      return this.user.fax.length <= 15
    },
    isEmailValid () {
      if (!this.user.email) return false
      return this.user.email.length <= 40
    },
    isLicenseValid () {
      if (!this.user.license) return false
      return this.user.license.length <= 20
    },
    isAddressValid () {
      if (!this.user.address) return false
      return this.user.address.length <= 60
    }
  },
  methods: {
    exam () {
      let that = this
      if (!that.aggreement) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B010'}, () => {
          utils.event.$emit('SCROLL_TO_TOP')
        })
        return
      }
      if (!that.$refs.registrationUploader.files || that.$refs.registrationUploader.files.length <= 0 || !that.$refs.cardUploader.files || that.$refs.cardUploader.files.length <= 0) {
        if (!that.user.registration || !that.user.card) {
          utils.event.$emit('SHOW_MESSAGE', {code: 'B006'})
          return
        }
      }
      that.$refs.registrationUploader.upload((registration) => {
        if (registration) {
          that.user.registration = registration.file
        }
        that.$refs.cardUploader.upload((card) => {
          if (card) {
            that.user.card = card.file
          }
          let user = {
            _id: manager.user._id,
            exam: that.user.toJSON()
          }
          utils.restPost('/private/updateUser', user).then(
            response => {
              if (response) {
                manager.user.signin(response)
                utils.event.$emit('SHOW_MESSAGE', {code: 'I006'}, () => {
                  that.$router.push({path: '/my'})
                })
              }
            }
          )
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
.content-block {
  width: 100%;
  height: 400px;
  overflow: auto;
  padding: 10px;
}
p {
  display: flex;
  align-items: center;
  .md-icon {
    font-size: 16px !important;
    margin: 0 8px 0 0;
  }
}
.aggreement {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 20px 0;
}
.upload-frame {
  max-width: 400px;
  left: 0px;
  right: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}
.upload-header {
  padding: 5px;
  text-align: center;
}
.action-container {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
  .action-button {
    width: 150px;
  }
}
</style>



