<template>
<md-card md-with-hover @click.native="showRequestDetail(object)">
  <md-ripple>
    <md-card-header>
      <md-avatar>
        <img src="/static/user.jpg">
      </md-avatar>
      <div class="md-title"></div>
      <div class="md-subhead">{{object.user}}</div>
    </md-card-header>

    <md-card-content>
      <div class="md-layout md-alignment-center-left">
        <div class="md-layout-item md-size-100">
          <div>{{object.generation}}0 {{object.nationality}} {{object.profession}}</div>
          <div>{{manager.const.type[object.type]}} <span class="isnew_new" v-if="object.isnew=='1'">{{object.isnewText}}</span><span class="isnew_old" v-if="object.isnew=='2'">{{object.isnewText}}</span></div>
          <div>:{{object.estatePriceFromText}}~{{object.estatePriceToText}}</div>
          <div>:{{object.line}} :{{object.station}} :{{object.requiredTime}}</div>
          <div>:{{object.comment}}</div>
        </div>
      </div>
    </md-card-content>

    <md-card-actions>
      <md-button @click.stop="contact(object)"></md-button>
    </md-card-actions>
  </md-ripple>
</md-card>
</template>

<script>
import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'object'],
  methods: {
    contact (request) {
      this.$router.push({name: 'chat'})
    },
    showRequestDetail (request) {
      utils.event.$emit('SHOW_BUY_REQUEST_DETAIL_DIALOG', request)
    }
  }
}
</script>

<style scoped lang="scss">
.md-card {
  margin: 0px;
}
.md-title {
  opacity: 0.7;
  font-size: 12px;
}
.md-subhead {
  font-size: 12px;
  opacity: 1;
  color: #111;
}
.isnew_new {
  color: yellow;
  background: red;
  border: 1px solid rebeccapurple
}
.isnew_old {
  color: white;
  background: green;
  border: 1px solid greenyellow
}
</style>



