<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary switch-config" @click="config.hide = !config.hide">
      <md-icon v-if="!config.hide">visibility_off</md-icon>
      <md-icon v-else>visibility</md-icon>
    </md-button>
    <verboseUploadFile class="file-uploader" ref="fileUploader" :manager="manager" :subFolder="manager.user._id + '/resources/download'" :next="uploaded" v-if="!user.downloads || user.downloads.length < 3"/>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div v-if="!config.hide">
    <div class="md-layout md-alignment-top-center" v-if="user.downloads && user.downloads.length > 0">
      <div class="md-layout-item md-size-30 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="(file, index) in user.downloads" :key="index">
        <fileCard class="animate" :manager="manager" :user="user" :file="file" :index="index"/>
      </div>
    </div>
  </div>
  <div class="visibility-off" v-else>
    
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import fileCard from '../shared/fileCard'
import verboseUploadFile from '@/components/common/verboseUploadFile'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    fileCard,
    verboseUploadFile
  },
  methods: {
    uploaded (file) {
      if (!this.user.downloads) this.user.downloads = []
      file.title = file.name.split('.')[0]
      this.user.downloads.push(file)
    }
  }
}
</script>

<style scoped lang="scss">
.file-uploader {
  position: absolute;
  top: 12px;
  right: 10px;
}
.md-layout {
  margin: 40px 0;
}
.md-layout-item {
  margin-bottom: 20px;
}
.switch-config {
  position: absolute;
  top: 6px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.visibility-off {
  width: 100%;
  height: 288px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e8e8e8;
}
</style>



