<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain add-shop md-primary" @click="user.addShop()">
      <md-icon>add</md-icon>
    </md-button>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <shopCard2 :manager="manager" :user="user" :shop="shop" :index="index" v-for="(shop, index) in user.shops" :key="index"/>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import shopCard2 from '../shared/shopCard2'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    shopCard2
  }
}
</script>

<style scoped lang="scss">
.add-shop {
  position: absolute;
  top: 6px;
  right: 6px;
}
</style>



