<template>
<div>
  <vue-headful title=" - "/>

  <div class="md-layout md-gutter" v-if="manager.inquiryService.inquiries.length > 0">
    <div class="md-layout-item md-size-25 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="inquiry in manager.inquiryService.inquiries" :key="inquiry._id">
      <inquiryCard :manager="manager" :inquiry="inquiry"/>
    </div>
  </div>
  <div v-else>
    <md-empty-state
      md-rounded
      md-icon="access_time"
      md-label=""
      md-description="">
    </md-empty-state>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import inquiryCard from '@/components/shared/inquiryCard'
export default {
  props: ['manager'],
  components: {
    inquiryCard
  }
}
</script>

<style scoped lang="scss">

</style>



