<template>
<div class="component-wrapper">
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-33 md-xsmall-size-100">
      <md-field md-clearable :class="{'md-invalid': !isNameValid}">
        <label for="name"></label>
        <md-input v-model="name" required></md-input>
        <span class="md-helper-text"></span>
        <span class="md-error" v-if="name === '' || (name && !name.trim())"></span>
        <span class="md-error" v-else>20</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-66 md-xsmall-size-100">
      <md-field md-clearable :class="{'md-invalid': !isContactValid}">
        <label for="name"></label>
        <md-input v-model="contact" required></md-input>
        <span class="md-helper-text"></span>
        <span class="md-error" v-if="contact === '' || (contact && !contact.trim())"></span>
        <span class="md-error" v-else>40</span>
      </md-field>
    </div>
    <div class="md-layout-item md-size-100">
      <md-field :class="{'md-invalid': !isContentValid}">
        <label></label>
        <md-textarea v-model="content" required></md-textarea>
        <span class="md-error" v-if="content === '' || (contact && !contact.trim())"></span>
        <span class="md-error" v-else>200</span>
      </md-field>
    </div>
  </div>
  <div class="action-container">
    <p class="md-body-1"><span v-if="user.email">{{user.email}}</span><span v-else></span></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain md-primary" @click="editMail = true">
      <md-icon>edit</md-icon>
    </md-button>
  </div>
  <p class="md-body-2"></p>

  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editMail"
    v-model="user.email"
    md-title=""
    md-input-maxlength="40"
    md-input-placeholder="Type..."
    md-confirm-text=""
    md-cancel-text=""
  />
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
export default {
  props: ['manager', 'user', 'config'],
  data: () => ({
    editMail: false,
    name: null,
    contact: null,
    content: null
  }),
  computed: {
    isNameValid () {
      if (this.name === null) return true
      if (!this.name || !this.name.trim()) return false
      if (this.name && this.name.trim().length > 20) return false
      return true
    },
    isContactValid () {
      if (this.contact === null) return true
      if (!this.contact || !this.contact.trim()) return false
      if (this.contact && this.contact.trim().length > 40) return false
      return true
    },
    isContentValid () {
      if (this.content === null) return true
      if (!this.content || !this.content.trim()) return false
      if (this.content && this.content.trim().length > 200) return false
      return true
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout-item {
  margin-top: 30px;
}
.action-container {
  margin-top: 30px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.md-body-1 {
  padding: 0px 20px;
  width: calc(100% - 60px);
}
.md-body-2 {
  padding: 0px 20px;
}
</style>



