<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title"></p>
    <md-button md-theme="default" class="md-fab md-mini md-plain add-service md-primary" @click="addService = true">
      <md-icon>add</md-icon>
    </md-button>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="md-layout">
    <div class="md-layout-item md-size-50 md-small-size-100" v-for="(service, index) in user.services" :key="index">
      <serviceCard :manager="manager" :user="user" :object="service" :index="index" :edit="true"/>
    </div>
  </div>

  <md-dialog :md-active.sync="addService">
    <md-dialog-content>
      <div class="md-layout md-gutter" v-if="hasNew">
        <div class="md-layout-item md-size-50 md-medium-size-100" v-for="(service, index) in manager.const.service" :key="index" v-if="!isAdded(service)">
          <serviceCard :manager="manager" :user="user" :object="service" :index="index" :edit="false"/>
        </div>
      </div>
      <div v-else></div>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button md-theme="default" class="md-primary" @click="addService = false"></md-button>
    </md-dialog-actions>
  </md-dialog>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import serviceCard from '../shared/serviceCard'
export default {
  props: ['manager', 'user', 'config'],
  components: {
    serviceCard
  },
  data: () => ({
    addService: false
  }),
  computed: {
    hasNew () {
      if (this.user.services) {
        for (let key in manager.const.service) {
          const service = manager.const.service[key]
          const isUsing = this.user.services.some(one => {
            return one.title === service.title
          })
          if (!isUsing) {
            return true
          }
        }
        return false
      }
      return true
    }
  },
  methods: {
    isAdded (service) {
      if (this.user.services) {
        return this.user.services.some(one => {
          return one.title === service.title
        })
      }
      return false
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout-item {
  margin-bottom: 10px;
}
.add-service {
  position: absolute;
  top: 0px;
  right: 0px;
}
</style>



